.about-header {
  position: relative;

  &__image {
    float: left;
    overflow: hidden;
    width: rem(386px);
  }

  &__image-details {
    background-color: $gray-f5;
    float: left;
    margin-top: rem(10px);
    padding: rem(15px);
    text-align: center;
    width: rem(386px);
    
    p {
      font-size: rem(11px);
      font-weight: 300;
      margin: 0;

      span {
        color: $lightest-grey;
        font-size: rem(15px);
        padding: 0 rem(15px);
      }
    }
  }

  &__information {
    float: left;
    padding-left: 0;
    width: 100%;
    

    h2 {
      color: $grey-3;
      font-size: rem(22px);
      font-weight: 700;
      line-height: rem(30px);
      margin: rem(10px) 0 0 0;
      padding-bottom: rem(20px);
    }

    p {
      color: $light-grey;
      font-size: rem(14px);
      font-weight: 300;
      line-height: rem(22px);
    }
  }
  @include breakpoint($tablet) {

    &__information {
      padding-left: rem(60px);
      width: calc(100% - 24.125rem);

      h2 {
        margin-top: 0;
      }
    }
  }
}
