.software-download, .manual-download {

  &__info {
    background-color: $gray-f5;
    padding: rem(15px) 0;
    margin-bottom: rem(20px);

    ul {
      padding: 0 rem(10px);
      li {
        list-style: none;
        &.download.btn {
          a {
            color: $white;
            text-decoration: none;
          }
        }
      }
    }
  }
  &__link {
    font-weight: bold;
    margin-bottom: rem(10px);
  }
  &__related-info {
    ul {
      li {
        list-style: disc;
      }
    }
  }
}
