.article-post {
  
  &__author-date {
    font-size: rem(20px);
    padding-bottom: rem(20px);
    border-bottom: rem(1px) solid $grey-3;
    margin:0;
  }

  .container .content {
    padding: rem(20px) 0;
  }

  &__image--sidebyside {
    display: flex; 
    flex-flow: row wrap; 
    justify-content: space-around;
  }

  &__image--large {
    figcaption {
      font-style: italic; 
      margin-bottom: rem(20px);
      text-align: center;
    }
  }

  &__more-stories {
    background-color: $vibrant-blue; 
    text-align: center; 
    padding: rem(20p) 0;

    a {
      color: $white; 
      font-size: rem(28px); 
      text-transform: uppercase;
      text-decoration: underline;
    }
  }
}
