.sales-tool {
  &.sales-tool--search-result {
    border: none;
  }

  .sales-tool--search-result--icons {
    margin: 0 rem(15px);
    padding: rem(25px) rem(25px) rem(13px) rem(25px);

    &-left {
      a {
        top: rem(-6px);
      }

      p {
        color: $white;
        top: 0;
        margin: 0;
        font-size: rem(12px);
      }

      .icon-downloads {
        padding-right: rem(5px);

        @include breakpoint($desktop) {
          padding-right: rem(20px);
        }
      }
    }

    &-right {
      @include breakpoint($desktop) {
        position: relative;
        top: rem(5px);
      }
    }
  }

  &--back {
    color: $vibrant-blue;
    font-size: rem(13px);
    padding-left: rem(15px);

    &:hover {
      text-decoration: none;
    }

    span {
      font-size: rem(11px);
      margin-right: rem(5px);
    }
  }

  &--body {
    margin: rem(25px) 0;
    width: 100%;

    @include breakpoint($desktop) {
      width: 85%;
    }
  }

  &--series {
    width: 100%;
    float: left;
    padding-top: rem(45px);

    &> div {
      float: left;
      clear: both;
      margin: 0 rem(55px) rem(25px) rem(15px);

      @include breakpoint($tablet) {
        margin-right: rem(150px);
        clear: none;
        max-width: 33%;
      }

      ul {
        padding: 0 0 0 rem(15px);
        margin: 0;
      }

      p {
        font-weight: 500;
      }
    }

    &> p {
      margin-left: rem(15px);
      float: left;
      clear: both;
    }
  }
}

#content-browser-filters {
  form {
    padding: 0;
    display: block;

    >div {
      label {
        font-size: rem(18px);
        font-weight: 500;
        margin-bottom: rem(16px);
        margin-top: rem(16px);
      }

      select {
        margin-bottom: rem(16px);
        border-radius: rem(4px);
      }

      input {
        margin-left: 0;
      }
    }

    >fieldset {
      legend {
        font-size: rem(18px);
        font-weight: 500;
        margin-bottom: rem(16px);
        margin-top: rem(16px);
      }
    }

    div, fieldset {
      width: 100%;
    }

    label {
      color: $black;
    }
  }
}

.sales-tool-curator {
  & > form {
    .form-item {
      display: inline-block;
      margin: 0 rem(20px) rem(20px) 0;

      @include breakpoint($mobile-only) {
        display: block;
      }
    }
  }
}
