/**
 * @file
 * Styles for Contact US section.
 *
 */

.prefooter {
  @extend %padding-v;
  background-color: $vibrant-blue;
  text-align: center;

  h2 {
    color: $white;
  }

  @include breakpoint($tablet) {
    h2 {
      font-size: rem(36px);
    }
  }
}

.contact-us {
  @extend %padding-top;

  &__block {
    @include grotesk-regular;
    text-align: center;

    a {
      display: block;

      span {
        @include helvetica-arial-bold;
        font-size: rem(15px);
      }

      span,
      &:before {
        color: $deep-charcoal;
      }

      &:hover,
      &:focus {
        text-decoration: none;

        span {
          text-decoration: underline;
        }
      }

      &:before {
        font-size: rem(48px);
      }

      h3,
      .description {
        color: $white;
      }

      h3 {
        @include grotesk-regular;
        font-weight: bold;
        font-size: rem(22px);
      }
    }
  }

  @include breakpoint($mobile-only) {
    &__block {
     margin-bottom: $grid-gutter-width;
    }
  }

  @include breakpoint($tablet) {
    @include display(flex);

    &__block {
      @include flex(1);
      padding-left: $grid-gutter-width / 2;
      padding-right: $grid-gutter-width / 2;
    }
  }
}
