/**
 * @file
 * Contact/Download sticky block.
 */

.callout {
  @include transition(all 0.5s ease-out);
  background-color: $darker-grey;
  padding-left: rem(50px);
  position: relative;

  &,
  a,
  p {
    color: $white;
  }

  header {
    p {
      color: $vibrant-blue;
    }
  }

  &__close {
    @include background-image(linear-gradient($light-green, $light-tk-blue));
    background-color: $light-green;
    color: $charcoal;
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: rem(30px);

    &:before {
      @include transform(translate(0, -50%));
      font-size: rem(22px);
      padding: rem(4px);
      position: absolute;
      top: 50%;
    }
  }

  h5 {
    @include gothic;
    border-bottom: rem(1px) solid $light-grey;
    font-size: rem(18px);
    padding-bottom: rem(10px);

    &:before {
      @extend %align-icon;
      margin: 0 rem(10px) 0 0;
    }
  }

  &__content {
    h6 {
      @include helvetica-arial-light;
      font-size: rem(12px);
      text-transform: uppercase;
    }

    .description {
      font-size: rem(12px);
      line-height: rem(18px);

      .icon-mail:before {
        font-size: rem(13px); // Fix FF icon cut off.
      }
    }

    p,
    h6 {
      margin-bottom: rem(10px);
    }

    input {
      @include helvetica-arial-light;
      font-size: rem(16px);
      padding-left: rem(20px);
    }
  }

  .col {
    padding: rem(5px);

    a {
      display: block;
      text-decoration: none;

      &:before {
        color: $grey-light-border;
        display: block;
        font-size: rem(34px);
      }
    }
  }

  .col-2,
  .col-1 {
    padding-right: rem(15px);
  }

  .btn {
    padding-bottom: rem(11px);
    padding-top: rem(11px);
  }

  .dropdown {
    margin: 0;

    &,
    .btn {
      width: 100%;
    }

    .btn {
      background-color: $white;
      color: $dark-gray;
      text-align: left;
    }

    ul {
      top: rem(35px); //Button height.
    }
  }

  .scroll-xs {
    padding-bottom:rem(20px);
  }

  @include breakpoint($mobile-only) {
    .col {
      border-top: rem(1px) solid $light-grey;
      padding-top: rem(10px);
      padding-left: rem(60px);
      position: relative;

      &:first-child {
        border: 0;
        padding-top: 0;
      }

      h6 {
        display: none;
      }

      a:before {
        font-size: rem(30px);
        position: absolute;
        left: rem(15px);
        top: 50%;
        margin-top: rem(-15px); // Half icon height.
      }
    }

    .col-1 {
      padding-top: 1rem;
    }
  }

  @include breakpoint($tablet) {
    &__content {
      @include display(flex);

      .download & {
        @include align-items(flex-end);
      }

      .col {
        border-left: rem(1px) solid $light-grey;
        text-align: center;

        &:first-child {
          border: 0;
        }
      }

      .col,
      .col-2 {
        @include flex(1);
      }
    }
  }
}

.sticky-btn-group {
  position: fixed;
  right: 0;
  top: 21%; // This value ensures the position under the header for small devices.
  z-index: 100;

  &.active {
    .icon {
      opacity: 0;
    }
  }

  @include breakpoint($tablet) {
    top: 45%;
  }
}

.sticky-btn {
  color: $white;
  margin-bottom: rem(1px);
  
  .icon {
    @include transition(all 0.5s ease-out);
    cursor: pointer;
    font-size: 0;
    opacity: 1;
    padding: rem(10px);
    position: absolute;
    right: 0;
    text-align: center;
    text-transform: uppercase;
    height: rem(44px);
    width: rem(50px);
    z-index: 1;
    
    &:before {
      display: block;
      font-size: rem(22px);
      margin-bottom: rem(10px);
    }

    &.icon-contact-us {
      @include background-image(linear-gradient(to top right, $light-green, $bright-green));
      background-color: $light-green;
    }

    &.icon-downloads {
      @include background-image(linear-gradient(to top right, $vibrant-blue, $light-green));
      background-color: $light-tk-blue;
      top: rem(45px);
    }
  }

  .callout {
    right: rem(-700px);
    opacity: 0;
    position: absolute;
    top: 0;
    width: rem(700px);
    max-width: 100vw;
  }

  @include breakpoint($mobile-only) {
    .download {
      width: rem($mobile-landscape);
    }
  }

  @include breakpoint($tablet) {
    .icon {
      font-size: rem(10px);
      height: rem(65px);
      width: rem(90px);

      &.icon-downloads {
        top: rem(66px);
      }
    }
  }
}

.show-callout {
  .callout {
    right: 0;
    opacity: 1;
    z-index: 2;
  }

  @include breakpoint($mobile-only) {
    .scroll-xs {
      max-height: 79vh; // The remaining space under the sticky buttons.
      overflow-y: auto;
    }
  }
}
