.banner {
  transform-style: preserve-3d;
  position: relative;
  overflow: hidden;
  word-wrap: break-word;

  .banner-container {
    padding: 0 rem($grid-gutter-width/2);

    @include breakpoint($tablet) {
      @include transform(translateX(-50%));
      @include display(flex);
      position: absolute;
      left: 50%;
      height: 100%;
      top: 0;
    }

    @include breakpoint($mobile-only) {
      width: 100%;
    }
  }

  .left-bottom,
  .right-bottom {
    @include align-items(flex-end);
  }

  .right-top,
  .right-bottom,
  .right-middle {
    @include justify-content(flex-end);
  }

  .left-middle,
  .right-middle,
  .center-middle {
    @include align-items(center);
  }

  .center-middle {
    @include justify-content(center);
    .banner__text {
      text-align: center;
      width:100%;
    }
  }
  
  &__subhead {
    clear: both;
  }

  &__img {
    font-size: 0;
    
    .slider--image & {
      margin-bottom: rem(15px);
    }
  }

  .btn {
    margin-top: rem(15px);
    
    &:hover {
      border: 1px solid $white;
    }
  }

  @include breakpoint($tablet) {
    .btn {
      margin-top: rem(30px);
    }
  }

  &--hero {
    .banner__text {
      color: $vibrant-blue;
      font-size: rem(32px);
      line-height: 1.3;
    }

    h1 {
      color: $black;
      font-size: rem(16px);
      text-transform: uppercase;
      padding-bottom: rem(10px);
      position: relative;

      &:before {
        content: '';
        display: block;
        width: rem(100px);
        height: rem(1px);
        background-color: $black;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
    
    &.title h1 {
      color: $vibrant-blue;
      text-transform: initial;
      font-size: rem(40px);
      &:before {
        background: none; 
      }
      @include breakpoint($desktop) {
        font-size: rem(50px);
      }
      
    }

    @include breakpoint($mobile-landscape) {
      .banner__text {
        font-size: rem(24px);
      }
    }

    @include breakpoint($tablet) {
      h1 {
        font-size: rem(19px);
      }

      .banner__text {
        font-size: rem(36px);
        padding: rem(40px) 0;
      }
    }

    @include breakpoint($desktop) {
      h1 {
        font-size: rem(24px);
      }

      .banner__text {
        font-size: rem(36px);
      }
    }
  }

  &--jumbo {
    .banner-container {
      background-color: $vibrant-blue;
    }

    .banner__text {
      @include helvetica-arial-light;
      font-size: rem(15px);
      padding-top: rem(20px);

      &,
      p {
        color: $grey-3;
      }
    }

    h1, h2 {
      font-size: rem(40px);
      line-height: rem(46px);
    }

    .btn {
      border-color: $white;
      margin-left: rem(5px);
      margin-right: rem(5px);
    }
  }

  &--hero,
  &--standard {
    .banner-container {
      background-color: $gray-f5;
    }
  }

  &--hero,
  &--jumbo,
  &--standard.full-width {
    .banner__text {
      @include make-md-column(7);
    }
  }

  &--hero,
  &--jumbo {
    .banner__text {
      @include make-lg-column(5);
    }
  }

  &--hero,
  &--jumbo,
  &--standard {
    .banner__text {
      padding: rem(20px) 0;
    }

    @include breakpoint($tablet) {
      .banner-container {
        background-color: transparent;
        width: 100%;
      }

      .banner__text {
        padding: rem(40px) 0;
        
        &.title-color--vibrant-blue {
          h1, h2 {
            color: $vibrant-blue;
          }
        }
  
        &.title-color--white {
          h1, h2 {
            color: $white;
          }
        }
      }
    }

    @include breakpoint($desktop) {
      .banner-container {
        padding-left: 5%;
        padding-right: 5%;
      }
    }
  }

  &--hero,
  &--jumbo {
    .js-slider-arrows-dots &,
    .js-slider & {
      .banner-container {
        @include breakpoint($mobile-landscape) {
          padding: 0 rem(65px);
        }

        @include breakpoint($tablet) {
          padding: 0 rem(95px);
        }
      }
    }

    @include breakpoint($desktop-wide) {
      .banner__img img {
        min-width: 100%;
      }
    }
  }

  &--standard {
    position: relative;

    h1, h2 {
      color: $vibrant-blue;
      font-size: rem(24px);
      line-height: rem(36px);
      margin-bottom: rem(10px);
      margin-top:0px;
      float: left;
    }

    .banner__text {
      font-size: rem(14px);
      line-height: rem(22px);
      
      p {
        clear: both;
      }
    }

    &.half-width,
    &.quarter-width {
      @include display(flex);
      @include flex-direction(column);
    }

    &.img-left {
      @include flex-direction(column-reverse);
    }
    
  

    @include breakpoint($tablet) {
      &.half-width,
      &.quarter-width {
        @include align-items(center);
        @include flex-direction(row-reverse);

        .banner__img {
          float: right;
        }

        .banner-container {
          @include transform(translateX(0));
          position: static;;
          display: block;
        }
      }

      &.half-width {
        .banner-container,
        .banner__img {
          width: 50%;
        }
      }

      &.quarter-width {
        .banner-container{
          width: 75%;
        }

        .banner__img {
          width: 25%;
        }
      }
    }

    @include breakpoint($tablet) {
      &.full-width {
        &,
        p,
        h1, h2 {
          color: $white;
        }
      }
    }

    @include breakpoint($desktop) {
      @include container-fixed;
      padding: 0;
      max-width: rem(1110px);

      h1, h2 {
        font-size: rem(28px);
      }
    }
  }

  &--video {
    position: relative;

    .video__wrapper {
      display: block; 
      position: relative; 
      max-width:100%;
    }

    .video-inner__wrapper {
      padding-top: 56.25%;
      position: relative
    }

    .video-banner__text {

      color: $white;
      padding-left: 15%;
      padding-right: 15%;
      text-align: center;
      width: 100%;

      @include breakpoint($mobile-only) {
        background: $grey-16;
      }
      @include breakpoint($tablet) {
        left: 0;
        position: absolute;
        top: 46%;
        transform: translateY(-50%);
        padding-left: 13%;
        padding-right: 13%;
      }

      @include breakpoint($desktop) {
        top: 35%;
      }

      h1 {
        font-size: rem(32px);
        margin: 0 auto rem(12px);
        padding-top: rem(15px);

        @include breakpoint($tablet) {
          font-size: rem(45px);
          margin-bottom: 30px;
        }

        @include breakpoint($desktop) {
          margin-bottom: rem(16px);
          font-size: rem(60px);
        }
      }

      p {
        @include grotesk-light;
        color: $white;
        font-size: rem(16px);
        line-height: rem(22px);

        @include breakpoint($tablet) {
          font-size: rem(18px);
          line-height: rem(22px);
          margin-bottom: rem(26px);
        }

        @include breakpoint($desktop) {
          line-height: rem(26px);
          margin-bottom: rem(40px);
        }
      }

      .btn {
        margin-bottom: rem(16px);
      }
    }
  }

  .view-mode-slider-item {
    img {
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.header-image {

  .banner .banner--standard {
    margin-bottom: rem(20px);
  }
  .banner-container {
    padding-left: 3%;
  }
  h1 {
    font-size: 1.7rem;
    font-weight: bold;
    margin: 0;
    line-height: 1.1;
  }

  h2 {
    margin-top: .5rem;
    font-size:1.5rem;
  }

  .banner__text {
    @include make-md-column(8);
  }

  @include breakpoint($tablet) {
     .banner--standard.full-width .banner__text {
        @include make-md-column(9);
        padding-left: 0;
     }
    .banner__text {
      h1, h2 {
        color: $light-grey;
      }
      
      h1 {
        font-size: 2rem;
        font-weight: bold;
        margin: 0;
        line-height: 1.1;
      }

      h2 {
        margin-top: 0;
        font-size: 1.3rem;
      }
    }
  }

  @include breakpoint($desktop) {
    .banner .banner--standard {
      max-width: rem(1140px);
    }
    .banner__text {
      h1 {
        font-size: 2.5rem;
      }
      h2 {
        font-size: 1.75rem;
      }
    }
  }

  &__none {
    border-bottom: rem(1px) solid $grey-e5;
    margin-bottom: 1.25rem;

    .page-title {
      border-bottom: none;
      padding-bottom: 0;
    }

    h2 {
      margin-top: 0.3125rem;
      font-size: rem(21px);
    }
  }

}

.banner__text {
  @include grotesk-light;
}

//sliders
.slider-wrap {
  &.bg-color {
    &--blue {
      background-color: $tk-blue;

      .node--home_page & {
        padding: rem(30px) 0 rem(10px) 0;
        min-height: rem(315px);
      }
      
      &,
      & h1, h2,
      & p {
        color: $white;
      }
    }

    &--grey {
      background-color: $gray-f5;
    }
  }

  @include breakpoint($mobile-only) {
    &.bg-color {
      &--blue {
        .banner-container {
          background-color: $tk-blue;
        }
      }

      &--grey {
        padding-top: 0;

        .banner-container {
          background-color: $gray-f5;
        }
      }
    }
  }

  @include breakpoint($mobile-landscape) {
    .js-slider,
    .js-slider-arrows-dots {
      .banner--standard {
        padding: 0 rem(65px);
      }

      .full-width .banner-container {
        padding: 0 rem(65px);
      }
    }

    .slider--multiple-items {
      padding: 0 rem(65px);
    }

    .js-slider-thumb .banner {
      padding: 0 rem(65px);
    }
  }

  @include breakpoint($tablet) {
    .js-slider,
    .js-slider-arrows-dots {
      .banner--standard {
        padding: 0 rem(95px);
      }

      .full-width .banner-container {
        padding: 0 rem(95px);
      }
    }

    .slider--multiple-items {
      padding: 0 rem(95px);
    }

    .js-slider-thumb .banner {
      padding: 0;
    }
  }

  .container & {
    @extend %margin-bottom;
    @extend %row-sm;
  }
}

.ad-banner {
  margin-top: rem(25px);

  img {
    @extend %center-block;
  }
}

.js-slider-arrows-dots,
.js-slider,
.slider--multiple-items {
  @include breakpoint($desktop-wide) {
    @include container-fixed;
    max-width: rem($container-xl);
  }
}

// Redirect banner.
.banner--redirect {
  @include helvetica-arial-light;
  background-color: $gray-f2;
  color: $modal-content;
  padding: rem($grid-gutter-width/2) rem(50px) rem($grid-gutter-width/2) rem($grid-gutter-width/2);

    h3 {
      @include helvetica-arial-regular;

      &:before {
        @extend %icons;
        @extend .icon-earth;
        display: inline-block;
        font-size: rem(24px);
        vertical-align: middle;
      }
    }
  
  .js-close {
    cursor: pointer;
    font-size: rem(28px);
    line-height: 0;
    position: absolute;
    top: rem($grid-gutter-width/2);
    right: rem($grid-gutter-width/2);

    &:before {
      color: $modal-content !important;
    }
  }

  @include breakpoint($desktop-wide) {
    padding: rem($grid-gutter-width/2) rem(70px) rem($grid-gutter-width/2) rem($grid-gutter-width);

    .js-close {
      right: rem($grid-gutter-width);
    }
  }
}

// The block is shown from tek_ip_country_block.js.
#block-tekipcountryblock {
  position: relative;
  display: none;

  @include breakpoint($desktop-wide) {
    margin: 0 rem(-$grid-gutter-width/2);
  }
}

// Class from migrated content.
.banner-desc {
  @extend %clearfix;
  background-color: $gray-f2;
  color: $light-grey;
  font-size: rem(14px);
  padding: rem(30px);

  .title {
    text-transform: uppercase;
    margin-bottom: rem(16px);
  }

  .btn {
    margin-top: rem(20px);
  }

  @include breakpoint($tablet) {
    .desc {
      float: left;
      width: 60%;
    }

    .btn {
      float: right;
    }
  }
}

.banner.sso-contact {
  .banner-container {
    background-color: transparent;
  }

  .banner__text {
    h1, h2 {
      @include helvetica-arial-semibold;
    }

    .description {
      padding-top: (5px);
      line-height: 1.1;
    }

    @include breakpoint($desktop) {
      h1, h2 {
        font-size: rem(35px);
      }
      .description {
        font-size: rem(16px);
      }
    }

    @include breakpoint($desktop-wide) {
      h1, h2 {
        font-size: rem(45px);
      }
      .description {
        font-size: rem(22px);
      }
    }
  }

  .sso-contact_btn {
    a {
      border-radius: rem(55px);
      font-size: rem(18px);
      background-color: $vibrant-green;
    }
    @include breakpoint($tablet) {
      position: absolute;
      right: rem(24px);
    }
  }
}

.page-title-sso-capabilities {
  display: none;
}
.node--advertisement_banner {
  clear: both;
}

.node--product_category, .node--home_page {
  .slick-track{
    @include display(flex);

    .slick-slide {
      @include display(flex);
      height: auto;
      align-items: center; //optional
      justify-content: center; //optional
      &.banner--hero, &.banner--jumbo {
        display: block;
      }
    }
  }
}
