.new-product__container {
    position: relative;
    
	}
.new-product-snipe {
    position: absolute;
    top: 0px;
    left:0px;
    margin-left:rem(-20px);
    z-index: 99;
    
    &.series_table {
      top: rem(45px);
      left: rem(4px);
      
      .btn--snipe--green {
        font-size: rem(10px);
        padding: rem(14px) rem(8px);
        width: rem(32px);
        height: rem(32);
      }
    }
    
    &.banner-snipe {
      position: static;
      margin-left: 0
    }
}
