.featured-content-wrapper {
  @extend %clearfix;
  background: $gray-f5;
  margin-bottom: rem(20px);
  padding: rem(15px);

  h3 {
    margin-top: 0;
  }
  .application-new & {
    margin-top:20px;
  }
}

.featured-content {
  padding-bottom: rem(20px);

  h4 {
    @include underlined-header;
    @include helvetica-arial-book;
  }

  &__top {
    @extend %clearfix;
    margin-bottom: rem(10px);

    img {
      display: block;
      max-width: rem(100px);
      margin: 0 0 rem(15px) 0;
    }
  }

  @include breakpoint($mobile-landscape) {
    &__top {
      img {
        float: left;
        margin: 0 rem(10px) rem(10px) 0;
      }
    }
  }
}
