.card__group {
  display: grid;
  padding:rem(15px) 0;
  grid-gap: rem(30px);
  grid-template-columns: 1fr;

  @include breakpoint($desktop-wide) {
    grid-template-columns: 2fr 1fr;
    &:nth-of-type(even){
      grid-template-columns: 1fr 2fr;
    }
  }
}

.card_container {
  
  &.span-1, &.span-2 {
    @include display(flex);
    grid-row-end: span 1;
    @include flex-direction(row);
    

    a {
      &.right-middle {
        .card__img {
          @include order(2);
          margin-left: auto;
        }
      }

       @include breakpoint($tablet) {
          
          &.right-middle {
            .card__img {
              padding-left: 0;
              padding-right: rem(20px);
            }
            &.full-bleed .card__img {
              padding-right:0;
            }
          }
          .card__img {
            padding-bottom: rem(20px);
            padding-right: 0;
          }

          &.full-bleed .card__img {
            padding-bottom:0;
          }
        }
    }   

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      padding-bottom: rem(20px);
    }
  }

  &.span-1  {
    @include breakpoint($tablet) {
      a {
        @include display(flex);

        .card__copy {
          min-width: rem(370px);
        }
      }
    }
  }

  &.span-2  {

    @include breakpoint($tablet) {
      a {
        @include display(flex);
      }
    }


    @include breakpoint($desktop-wide) {
      @include flex-direction(column);
      grid-row-end: span 2;

      a {
        @include display(flex);
        @include flex-direction(column);
        grid-row-end: span 2;
        height: 100%;
        .card__img {
          padding-right: 0;
        }

        &.bottom {
          .card__img {
            @include order(2);
            margin-top: auto;
            padding-left: rem(20px);
          }
          &.full-bleed .card__img {
            padding-left:0;
          }
        }
      }
    }

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      a {
        @include flex-direction(row);
      }
    }
  }

  .card {
    background-color: $white;
    text-decoration: none;
    color: black;
    box-shadow: rem(-1px) rem(3px) rem(36px) 0 rgba(153,153,153,1);

    &:hover {
      background-color: $light-blue;
      text-decoration: none;
    }

    &.full-bleed {
      .card__img {
        padding: 0;
        img {
          width: rem(370px);
        }
      }
    }

    &__img {
      width: 100%;
      padding: rem(20px) rem(20px) 0 rem(20px);

      img {
        width: rem(330px);
      }

      @include breakpoint($tablet) {
        img {
          max-width: initial;
        }
      }
    }

    &__copy {
      padding: rem(20px);
    }

    h2.card__title {
      font-weight: bold;
      margin: 0;
      text-transform: uppercase;
      font-size: rem(21px);
    }

    &__description{
      padding-top: rem(10px);
      font-size: rem(14px);
      line-height: 1.4;
    }
  }
  
}

.article-tiles-page .tiles__container {
   @include gradient-vertical($white,$gray,0%,80%);
}
