div.model {
  padding-left: 0;
  padding-right: 0;
}

.model {
  @extend %padding-v;
  @include helvetica-arial-light;
  border: $base-border;

  .views-row:nth-child(even) & {
    background-color: $white;
  }

  &__label {
    padding-bottom: rem(10px);
    border-bottom: rem(1px) solid $gray-c;

    div {
      @include helvetica-arial-bold;
      letter-spacing: rem(1px);
      font-size: rem(11px);
    }
  }

  &__value {
    font-size: rem(12px);
    padding-top: rem(10px);
  }

  &__label,
  &__value {
    @include display(flex);
    @include align-items(center);

    div {
      @include flex(1);
      padding: 0 rem(10px);
    }
  }

  &__left {
    font-size: rem(18px);
    padding-left: rem(25px);
    margin-bottom: rem(10px);
  }

  &__name {
    @include gothic;
    font-size: rem(18px);
    line-height: rem(26px);
    margin: 0;
  }

  &__price {
    div:first-child {
      font-size: rem(11px);
    }
  }

  &__img {
    float: left;
    max-width: 50%;
    padding: 0 rem(15px) rem(15px) 0;
  }

  &__right {
    font-size: rem(11px);
    
    & > div {
      float: right;
    }

    &__configure a {
      font-size: rem(16px);
      line-height: rem(22px);
      margin-left: rem(30px);
    }

    &__price div:nth-child(2) {
      font-size: rem(18px);
    }
  }

  @include breakpoint($phone-only) {
    .model__right__price {
      float: left;
    }

    .scroll-table-wrap {
      & > div {
        min-width: rem(543px);
      }
    }
  }
}
