// Product Finder Series Styles

.product-finder-series {
  border: $base-border;
  padding: rem(10px) rem(15px);
  position: relative;
  height: rem(95px);
  border-bottom: none;

  &__name {
    @include helvetica-arial-light;
    font-size: rem(22px);
    line-height: rem(28px);
    padding: rem(9px) 0;

    a {
      color: $secondary-blue2;
      font-family: $heading-font-family;
    }
  }

  label {
    letter-spacing: rem(1.5px);
    margin: rem(5px) 0;
  }

  &__matched-models {
    text-decoration: underline;
  }

  &__dropdown {
    position: relative;
    border: $base-border;
    padding: rem(10px) rem(15px);

    span {
      color: $secondary-blue2;
      @include transform(rotate(90deg));
      display: inline-block;
      margin-right: rem(10px);
    }
  }

  &__count {
    color: $grey-blue;

    div,
    p {
      display: inline-block;
      margin: 0;
    }
  }
}

// Product Finder Model Styles

.product-model {
  @extend %clearfix;
  border: $base-border;
  font-size: rem(11px);
  margin-bottom: rem(10px);
  padding: rem(10px) rem(20px);

  &__header {
    @extend %clearfix;
    margin: 0 0 rem(20px) 0;

    h4 {
      a {
        color: $secondary-blue2;
      }
    }
  }

  &__price {
    @include helvetica-arial-light;
    padding-bottom: rem(16px);

    div {
      color: $lightest-grey;
    }

    h4 {
      margin: 0;
    }
  }

  &__spec {
    min-width: rem(500px);
  }

  @include breakpoint($mobile-landscape) {
    &__header {
      h4 {
        float: left;
      }
    }

    &__price,
    .btn {
      display: inline-block;
      vertical-align: middle;
    }

    &__price {
      padding-top: rem(16px);
      padding-right: rem(20px);
    }

    .right {
      float: right;
    }
  }
}

.product-finder-block {
  @extend %margin-bottom;
  @include gradient-vertical($white, $grey-bg);
  padding: rem(20px) 0;
  border-top: rem(1px) solid $grey-e5;
  border-bottom: rem(1px) solid $grey-e5;
}

.tek-product-finder-form {
  label {
    @include helvetica-arial-regular;
    padding-bottom: rem(20px);
  }

  select {
    float: left;
    width: 50%;
    margin-right: rem(20px);
  }

  @include breakpoint($mobile-only) {
    margin-bottom: rem(20px);
  }

  @include breakpoint($tablet) {
    select {
      width: 70%;
    }
  }
}
