.service-solutions-header {
  padding-bottom: rem(20px);
 

  &__information {
    h2 {
      color: $grey-3;
      font-size: rem(22px);
      font-weight: 700;
      line-height: rem(30px);
      margin: rem(10px) 0 0 0;
      padding-bottom: rem(20px);
    }
  }

  @include breakpoint($tablet) {
    &__information {
      h2 {
        margin-top: 0;
      }
    }
  }

  .app-featured-content-box {
    background-color: $gray-f5;
  }
}
