.app-teaser {
  @include helvetica-arial-light;
  border-bottom: rem(1px) solid $grey-e5;
  padding: rem(24px) 0;
  overflow: hidden;

  &__img {
    float: left;
    max-width: rem(80px);
    padding: 0 rem(25px) rem(20px) 0;
  }

  .descr {
    color: $light-grey;
    clear: both;
    font-size: rem(15px);
  }

  h4 {
    @include helvetica-arial-light;
    font-size: rem(16px);
    margin-bottom: rem(20px);
  }

  .icon {
    &:before {
      color: $vibrant-blue;
      font-size: rem(55px);
    }
  }

  @include breakpoint($mobile-landscape) {
    &__img {
      max-width: rem(100px);
    }

    &__txt {
      padding-left: rem(100px);

      .descr {
        clear: none;
      }

      h4 {
        margin-top: 0;
      }
    }

    .icon {
      &:before {
        font-size: rem(75px);
      }
    }
  }
}

.app-info-wedge {

  width: 100%;
  @include gradient-radial( circle at top right, $vibrant-blue, $dark-blue );
  padding: rem(20px);
  color: $white;
  clear: both;

  @include breakpoint($tablet) {
    padding: rem(90px);
  }

  &__header {
    text-align: center;
    padding-bottom: rem(70px);
  }

  .app-info-wedge__item {
    padding-bottom: rem(30px);
    &--icon {
      float: left;
      max-width: rem(80px);
      padding: 0 rem(20px) 0 0;

      &:before {
        color: $white;
        font-size: rem(55px);
      }
    }
  
    &--txt {
      padding-left: rem(40px);

      h4 {
        @include helvetica-arial-light;
        font-size: rem(20px);
        margin-bottom: rem(20px);

        a {
          color: $white;
        }
        &:after {
          content: '';
          display: block;
          width: rem(32px);
          height: rem(1px);
          background-color: $vibrant-blue;
          margin-top: rem(15px);
          margin-left: rem(36px);
        }
      }

      .descr, .btn--arrow {
        margin-left: rem(36px);
      }
      .descr, p {
        color: $white;
        font-size: rem(15px);
        clear: both;
      }
    }
  }


}

@include breakpoint($desktop) {
  .field--field_application_list_item div:nth-child(4n) {
    clear:both;
  }
}
@include breakpoint($mobile-only) {
  .field--field_application_list_item div:nth-child(3n) {
    clear:both;
  }
}

.related-app {
    color: $charcoal;
    clear: both;

    &__header {
      font-size: rem(29px);
      color: $tk-blue;
      padding-bottom: rem(20px);
    }

    &__item {
      padding-bottom: 0;
      text-align:center;
      &--icon {
        padding: 0;
        margin: 0 auto;

        &:active, &:hover, &:focus {
          color: transparent
        }

        &:before {
          color: $vibrant-blue;
          font-size: rem(100px);
        }
      }
      &--txt {

        h4 {
          @include helvetica-arial-light;
          font-size: rem(16px);
          margin-bottom: 0;
          width:80%;
          text-align: center;
          margin: 16px auto;

          a {
            color: $vibrant-blue;
          }
        }

        .descr, .btn--arrow {
          margin-left: rem(36px);
        }
        .descr, p {
          color: $charcoal;
          font-size: rem(15px);
          clear: both;
        }
      }
    }
  }

[class*="app-more"] {
  .more-info {
    position: relative;
  }

  [class^="icon-"] {
    padding-left: rem(22px);
    position: relative;
  }

  [class^="icon-"]:before, a.more-info img {
    font-size: rem(16px);
    left: 0;
    position: absolute;
    top: 0;
  }

}

