.application-header {
  padding-bottom: rem(20px);

  &.row {
    margin:0;
  }
  
  &__cta {
    background: $gray-f5;
    padding: rem(20px);

    &--title {
      position: relative;
      font-size: rem(38px);
      padding-left: rem(20px);
      font-weight: 200;
      color: $dark-gray;

      &:after {
        content: '';
        display: block;
        margin-left: rem(20px);
        width: rem(220px);
        height: rem(3px);
        position: absolute;
        left: 0;
        bottom: rem(-10px);
        @include gradient-horizontal ($vibrant-blue, $mid-green);
      }
    }

    ul {
      font-size: rem(19px);
      list-style: none;

      li {
        line-height: rem(24px);

        &:before {
          content: "";
          border-color: transparent $vibrant-blue;
          border-style: solid;
          border-width: rem(6px) 0 rem(6px) rem(8ox);
          display: block;
          height: 0;
          width: 0;
          left: rem(-18px);
          top: rem(18px);
          position: relative;
        }

        a {
          font-weight: 400;
        }
      }
    }  
  }

  &__featured-content {
    background: $tk-blue;
    // Styles for migrated classes.
    h2,
    .heading {
      @include grotesk-light;
      font-size: rem(22px);
      margin: 0 0 rem(20px) 0;
    }

    .desc {
      font-size: rem(14px);
      margin-bottom: rem(20px);
    }

    .card-ad-in {
      @extend %clearfix;
      padding: 0 rem(20px) rem(20px);
      color: $white;

      .btn {
        margin: rem(10px);
      }
    }

    img {
      margin-bottom: rem(10px);
      width: 100%;
    }
  }

  &__information {
    h2 {
      color: $grey-3;
      font-size: rem(22px);
      font-weight: 700;
      line-height: rem(30px);
      margin: rem(10px) 0 0 0;
      padding-bottom: rem(20px);
    }
    
    .h7 {
      color: $base-font-color;
      font-size: rem(14px);
      margin: rem(10px) 0;
    }
  }

  @include breakpoint($mobile-landscape) {
    &__featured-content {
      img {
        float: left;
        width: auto;
        margin-right: rem(15px);
      }
    }

    .card-ad-in {
      padding-top: $small-spacing;
    }
  }

  @include breakpoint($tablet) {
    &__information {
      h2 {
        margin-top: 0;
      }
    }

    &__featured-content {
      img {
        width: 100%;
      }
    }
  }

  &__box-ad {
    margin-top: rem(20px);
  }
}

