.rma-steps {

  margin: rem(40px) 0 rem(30px) 0;
  overflow: hidden;
  padding: 0;

  li {
    list-style-type: none;
    list-style-image: none;
    color: $lightest-grey;
    text-transform: uppercase;
    font-weight: bold;
    float: left;
    width: rem(200px); 
    height: rem(34px);
    padding: rem(7px) rem(5px) rem(7px) rem(40px);
    border-right: rem(20px) solid $white;
    position: relative;
    background-color: #eeeeee;

    &.active { 
      background-color: $tk-blue; 
      color: white; 

      &::after { 
        border-left-color: $tk-blue; 
      }
    }

    &:first-child {
      padding-left: rem(10px);
    }

    @include breakpoint($tablet) {
      &:nth-child(n+2)::before {
        left:0;
        @include steps-arrowed-nav($white);
      }

      &::after {
        z-index: 1;
        right: rem(-25px);
        @include steps-arrowed-nav($light-grey-e);
      }
    }
  }

  @include breakpoint($mobile-only) {
    li {
      font-size: 0.8em;
      padding: rem(10px);
      border-right: rem(5px) solid $white;
      width: auto;
      margin-top:rem(5px);
    }
  }
}

.tek-service-rma-request-form {

  h3 {
    margin-bottom: 0;
  }

  table {

    th {
      padding: rem(8px);

      &:nth-child(2) {
        width: rem(400px);
      }

      &:nth-child(4) {
        width: rem(140px);
      }

      &:nth-child(6) {
        white-space: nowrap;
      }
    }

    td {
      padding:rem(8px);
      vertical-align: top;

      @include breakpoint($mobile-only) {
        .bt-content {
          width: 100%;
          margin: 0 rem(20px);
        }
      }
    }

    .description {
      font-size: rem(12px);
    }
  }

  .rma__instruction {
    font-size: rem(15px);
  }

  .form-item-p-service-details, .form-item-files {
    margin: rem(25px) 0;
  }

  textarea {
    height: auto;
  }

  .form-managed-file {
    margin: rem(17px) 0;
  }

  input.invisible {
    display: none;
  }

  input.form-submit {
    margin-top: rem(20px);
  }

  @include breakpoint($mobile-only) {
    #edit-clear, #edit-add-more, #edit-get-addr, #edit-next, #edit-go-back, #edit-submit {
      clear: both;
      margin-top: rem(20px);
    }
  }
}
