.promotion-filter {
  
  &__container {
    border-top: rem(1px) solid $gray;
    @include gradient-vertical($white, $grey-bg);
    padding: rem(20px) rem(40px);
    overflow: hidden;
  }

  .form-item-region, .form-item-product {
    float: left;
    padding: rem(8px) rem(16px);
    @include breakpoint($mobile-only){
      clear: both;
    }

    label {
      text-transform: uppercase;
      display: inline-block;
      float: left;
      padding: rem(8px) rem(5px);
    }

    select {
      width: 76%;
    }
  }

  .form-item-region  {
    width: 35%;
    @include breakpoint($mobile-only){
      width: 100%
    }
  }

  .form-item-product  {
    width: 40%;
    @include breakpoint($mobile-only){
      width: 100%
    }
  }

  &__separator {
    float: left;
    @include breakpoint($mobile-only){
      clear: both;
      padding:rem(10px) 0;
    }
  }

  &__or {
    background: $grey-border;
    padding: rem(7px) rem(14px);
    border-radius: rem(80px);
    color: $white;
    width: rem(50px);
    height: rem(50px);
    font-size: rem(22px);
    position: relative;
    margin: rem(5px) rem(40px);
    float: left;

    &::before, &::after {
      content: "";
      position: absolute;
      border: rem(1px) solid $grey-border;
      left: rem(23px);
      height: 33%;
    }

    &::before {
      top: rem(-15px);
    }

    &::after {
      bottom: rem(-15px);
    }
  }

  &__button {
    padding-top: rem(20px);
    clear: both;
    .button {
      background: $grey-border;
      :hover {
        bacground: $grey-border;
      }
    }

    @include breakpoint($desktop) {
      clear:none;
      padding-top: rem(10px);
    }
  }
}

.promotion-listing {
  padding: rem(20px) rem(10px);
  margin-bottom: rem(10px);
  min-height: rem(360px);

  &:nth-child(odd) {
    clear: both;
  }
  img {
    float: right;
    display: inherit;
    margin-left: rem(10px);
  }
  &__border{
     border-top: rem(4px) solid $medium-gray;
     padding-bottom:  rem(20px);
   }  
  &__expire {
    margin: rem(10px) 0;
  }
  &__description {
    margin-bottom: rem(20px);
  }
}    

.promotions {
    &_all {
        padding-bottom: rem(20px);
    }
    &_image picture img {
    padding-bottom: rem(10px);
    margin:rem(0px);
    width:100%;
    }
    &_valid {
    padding-bottom:rem(20px);
    }
    &_cta{
    padding-bottom:rem(10px);
    }
} 
