.featured-solution {
  background-color: $gray-f5;

  h1 {
    @include grotesk-regular;
    color: $grey-3;
    line-height: 1.25;
    margin-bottom: rem(20px);
  }

  &__link {
    @include helvetica-arial-light;
    font-size: rem(15px);
    display: block;

    &:before {
      @include triangle(5px, $vibrant-blue, 'down');
      margin-right: rem(5px);
    }
  }
}

.featured-solutions {

  &__container {
    width: 100%;
    @include gradient-radial( circle at top right, $light-grey, $deep-charcoal );
    padding: rem(20px);

    @include breakpoint($tablet) {
      padding: rem(90px);
    }

   .descr  {
      h2, h3, p {
        color: $gray;
      }
      margin-top: rem(20px);
      font-size: rem(28px);
    }

   .tile__wrapper--horizontal {
      .tile {
        h2 {
          color: $vibrant-blue;
          font-weight: bold;
          margin-bottom: rem(7px);
          min-height:0;
          &:after {
            display: none;
          }
        }
       &__description {
        p {
          color: $gray;
          font-size: rem(22px);
        }
       }
      }
    }

  }

  &__header {
    color: $vibrant-blue;
    &:after {
      content: '';
      display: block;
      width: rem(64px);
      height: rem(1px);
      background-color: $vibrant-blue;
      margin-top: rem(15px);
    }
  }
}
