.buy-quote {
  @extend %clearfix;
  background-color: $deeper-charcoal;

  .left-col {
    font-size: rem(20px);

    a {
      color: $white;
    }
  }

  .right-col {
    ul {
      @extend %reset-list;
      padding-bottom: rem(20px);

      a {
        @include grotesk-regular;
        color: $vibrant-blue;
        display: block;
        font-size: rem(14px);
        padding-bottom: rem(5px);
        text-transform: uppercase;
      }
    }

    .btn {
      @include breakpoint($mobile-landscape) {
        float: right;
      }
    }
  }

  .call-number {
    text-align: center;
  }

  .thumbnails-wrap {
    @extend %margin-bottom;

    a {
      @extend %thumb-link-icon;
      font-size: rem(14px);

      &:hover {
        text-decoration: none;
      }
    }
  }

  @include breakpoint($tablet) {
    .left-col {
      border-right: rem(1px) solid $grey-border;
      font-size: rem(24px);
      text-align: center;
    }

    .right-col {
      padding-left: rem(40px);
      padding-bottom: rem(40px);

      ul {
        padding-bottom: rem(10px);
      }
      
      .btn {
        position: absolute;
        left: rem(40px);
        bottom: 0;
      }
    }

    .thumbnails-wrap {
      @include display(flex);

      a {
        @include flex(1);
        font-size: rem(14px);
        margin: 0 rem($grid-gutter-width/2);
      }
    }
  }
}
