.sales-tool--search-result {
  border: rem(1px) solid $gray;
  border-radius: rem(5px);

  .new {
    text-transform: uppercase;
    background-color: $orange;
    color: $white;
    float: right;
    padding: rem(2px) rem(4px);
    font-size: rem(12px);
    border-radius: 0 rem(4px) 0 0;
  }

  &--top {
    padding: rem(15px);

    .document-type {
      font-style: italic;
      color: $table-grey;
    }

    a {
      color: $deep-charcoal;
      font-weight: 900;
    }

    @include breakpoint($desktop) {
      h4 {
        max-width: 85%;
      }
    }

    &--details.hidden-lg {
      td {
        border: none;
        padding: 0;

        p {
          margin: 0;
          color: $deep-charcoal;
        }

        &:first-child {
          min-width: rem(115px);
          width: 25%;
        }
      }

      td.outdated-flag {
        p {
          color: $orange;
        }
      }
    }

    &--details.visible-lg {
      max-width: 85%;

      td {
        border: none;
        padding: 0;
        max-width: 0;

        p {
          margin: 0;
          color: $deep-charcoal;
        }
      }

      td.outdated-flag {
        p {
          color: $orange;
        }
      }
    }
  }

  &--icons {
    padding: rem(15px);
    background-color: $gray-f5;
    border-radius: 0 0 rem(5px) rem(5px);

    a, p, img {
      display: inline-block;
    }

    a:hover {
      opacity: .75;
    }

    &-left {
      display: inline-block;

      div {
        display: inline-block;
      }

      a {
        margin-right: rem(25px);
      }

      p {
        position: relative;
        top: rem(-5px);
        margin-left: rem(5px);
        margin-bottom: 0;
        font-weight: 500;
        color: $vibrant-blue;
      }
    }

    &-right {
      display: inline-block;
      text-align: right;
      float: right;

      &>div {
        display: inline-block;
        margin-left: rem(20px);

        &:first-child {
          @include breakpoint($mobile-only) {
            margin-left: 0;
          }
        }

        p {
          position: relative;
          top: rem(-5px);
          margin-left: rem(5px);
          margin-bottom: 0;
          color: $table-grey;
        }
      }
    }

    .no-visibility {
      visibility: hidden;
      max-width: rem(0px);
      margin-right: rem(-70px);
    }
  }
}

.top-downloads {
  .sales-tool--search-result {
    margin: rem(20px) 0;
  }
}

.tek-tabs {
  .sales-tool--search-result--top {
    background-color: $white;
    border-radius: rem(5px) rem(5px) 0 0;
  }

  .sales-tool--search-result--top--details.visible-lg {
    max-width: 100%;
  }
  
  .search-page-box.sales-tool {
    display: none;
  }
}

.search-view__results {
  float: left;

  &>h4 {
    text-transform: uppercase;
    color: $table-grey;
  }
}

#sales-tools-mobile-facet-form {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  background-color: $white;
  z-index: 1000;
  margin-left: rem(-15px);
  padding: rem(15px);
  overflow-y: auto;
  padding-bottom: rem(60px);

  h4:first-child {
    margin-bottom: rem(10px);
  }

  label.option {
    color: $deep-charcoal;
  }

  fieldset {
    margin: rem(16px) 0;

    legend {
      font-size: rem(18px);
      margin: rem(16px) 0;
    }
  }

  .filter-button--container {
    background-color: $darker-grey;
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 10000;
    margin-left: rem(-15px);
    padding: rem(15px) rem(20px);

    .btn.btn--green {
      background-color: $bright-green;
      width: 70%;
      float: right;
    }

    .btn.btn--white {
      background-color: $white;
      color: $darker-grey;
      width: 25%;
    }
  }

  select {
    border-radius: rem(4px);
  }
}

.search-view__results {

  @include breakpoint($tablet) {
    border-left: 0.0625rem solid $grey-e5;
  }
}

.search-view__filter {
  p.filter-toggle {
    color: $vibrant-blue;
    font-size: rem(13px);
    font-weight: 700;
    margin: rem(10px) 0;
    display: block;
    text-align: right;

    img {
      display: inline-block;
      margin-right: rem(10px);
    }
  }
}

#sales-tools-search-tabs {
  padding-top: rem(16px);
  border-bottom: 0.0625rem solid $grey-e5;

  p {
    display: inline-block;
  }

  a {
    display: inline-block;
    margin-left: rem(20px);
    padding-bottom: rem(16px);
    font-weight: 500;

    &:hover {
      text-decoration: none;
      color: $blue-hover;
    }

    &.active, &:hover {
      border-bottom: rem(2px) solid $vibrant-green;
    }
  }
}

.search-page-box {

  h2 {
    font-weight: 700;
  }

  select {
    border-radius: rem(4px);
    width: 100%;
    display: inline-block;
  }

  @include breakpoint($tablet) {

    .product-series-select {
      width: 48%;
      margin-right: 1%;
      display: inline-block;
    }

    .sales-tool-product-category {
      width: 48%;
      display: inline-block;
      float: right;
    }

  }
}

.view--sales-tools-my-favorites {
  .views-row {
    margin-bottom: rem(20px);
  }
}

.field--field_resource_url {
  .icon-downloads {
    font-size: rem(14px);
    float: left;
    font-weight: 500;

    &::before {
      font-size: rem(20px);
      margin-right: rem(10px);
      position: relative;
      bottom: rem(-3px);
      color: $table-grey;
      font-weight: 700;
    }

    &:hover {
      text-decoration: none;
    }
  }
}
