.award-description {
  @extend %row-sm;
  background-color: $light-grey-e;
  border-bottom: $base-border;

  &__txt {
    h3 {
      @include helvetica-arial-light;
      font-size: rem(15px);
      margin: 0 0 rem(5px);
    }
  }

  p {
    margin: 0;
  }

  > div {
    padding: rem($grid-gutter-width/2);
  }

  @include breakpoint($tablet) {
    @include display(flex);
    @include align-items(center);

    &__txt {
      width: 60%;
    }

    &__img,
    &__date {
      width: 20%;
    }
  }

  @include breakpoint($desktop) {
    &__txt {
      width: 70%;
    }

    &__img,
    &__date {
      width: 15%;
    }
  }

  .view--awards-all-page__head & {
    background-color: $tab-select;
    border-bottom: rem(3px) solid $gray-c;

    &, p {
      @include helvetica-arial-bold;
      color: $base-font-color;
    }
  }
}
