/**
 * @file
 * sass/components/_popups.scss
 *
 * CSS rules that style design components (and their associated states and themes.)
 */

// Drupal-modal.
.ui-widget-header {
  background: none;
  border: none;
  border-bottom: rem(1px) solid $grey-e5;

  .ui-button {
    &,
    & span {
      outline: none;
    }
  }
}

.ui-dialog {
  max-width: 100%;

  .ui-dialog-content {
    //height: auto !important;
  }

  @include breakpoint($mobile-only) {
    .ui-dialog-content {
      width: auto !important;
      padding: rem(10px);
    }
  }
}

.probe-comparison-popup {
  width: rem(900px) !important;

  header p {
    @extend %padding-top;
    @include grotesk-light;
    color: $base-font-color;
    font-size: rem(28px);
  }

  table {
    table-layout: fixed;

    tr, td {
      padding: rem(10px);
    }
  }

  .ui-dialog-content {
    min-height: rem(350px) !important;
  }

  .ui-dialog-titlebar {
    padding-top: rem(30px);

    .ui-dialog-title {
      display: none !important;
    }
  }
}

// Product Series redesign modal.
.product-series {
  .ui-dialog {
    background-color: transparent;
    border: 0;
    border-radius: 0;
    padding: 0;
  }

  .ui-dialog-content {
    background-color: $white;
  }

  .ui-dialog-titlebar {
    border: 0;
  }

  .ui-dialog-titlebar-close {
    @extend %icons;
    @extend .icon-close;
    background: none;
    border: 0;
    color: $vibrant-blue;
    font-size: rem(22px);

    &:before {
      font-weight: bold;
    }
  }

  .ui-icon {
    background: none;
    height: rem(22px);
    width: rem(22px);
  }

  .ui-widget-overlay {
    background: $black;
    opacity: .85;
  }
}

// Colorbox.
#cboxWrapper,
#cboxContent,
#cboxLoadingOverlay {
  border: 0;
  background: transparent;
}

#cboxLoadedContent {
  background: $white;
}

#cboxClose {
  @extend %icons;
  @extend .icon-close;
  background: none;
  color: $vibrant-blue;
  display: block;
  font-size: 0;
  text-indent: 0;
  top: 0;

  &:before {
    display: block;
    font-size: rem(22px);
    font-weight: bold;
  }
}

#cboxLoadedContent {
  margin-top: rem(28px);
}
