.model-container > div:nth-child(odd) {
  background-color: $gray-f5;
}

.probe {
  border: rem(1px) solid $light-gray;

  &__table {
    width: 100%;
    padding: 0;

    &-data {
      padding: rem(5px);
    }

    &-header {
      width: 10%;
      padding: rem(5px);
    }
  }

  &_btn_config {
    margin-bottom: rem(5px);
  }

  &_info {
    padding-top: rem(25px);
  }

  &_mobile_specs {
    text-align: center;
    margin: rem(21px) 0 rem(15px) 0;
  }

  &_right_cta {
    text-align: center;
  }

  &_view_larger {
    font-size: rem(13px);
    padding-bottom: rem(5px);
    white-space: nowrap;
  }

  &_view_compatible {
    text-align: center;
    font-size: rem(11px);
    padding-top: rem(10px);

    a {
      color: $dark-blue;
    }
  }
  &_fam_datasheet {
    font-size: rem(14px);
  }

  &_img {
    margin-bottom: rem(10px);
  }

  &__right {
    clear: none;
    font-size: rem(11px);
    max-height: rem(1000px);
    overflow: hidden;

    & > div {
      float: right;
    }

    &__configure a {
      font-size: rem(16px);
      line-height: rem(22px);
      margin-left: rem(30px);
    }

    &__price div:nth-child(1) {
      margin-bottom: rem(20px);
      font-size: rem(20px);
    }
    &__price div:nth-child(2) {
      font-size: rem(10px);
    }
    &__price div:nth-child(3) {
      font-size: rem(14px);
    }
  }

  &__value {
    font-size: rem(12px);
    padding-top: rem(10px);
  }
  &__noPadding {
    padding: rem(0px) !important;
    margin: rem(0px) !important;
  }

  &__label,
  &__value {
    @include display(flex);
    @include align-items(center);

    div {
      @include flex-grow(1);
      @include flex-basis(0);
      padding: 0 rem(10px);
    }
  }

  &__left {
    font-size: rem(18px);
    padding-left: rem(25px);
    margin-bottom: rem(10px);
  }

  &__name {
    @include gothic;
    font-size: rem(18px);
    line-height: rem(26px);
    margin: 0;
  }

  &__price {
    div:first-child {
      font-size: rem(11px);
    }
  }

  &__img {
    float: right;
    max-width: 100%;
    padding: 0 rem(15px) rem(15px) 0;
  }

  &__table {
    display: none; // Table is displayed on click from js.

    table {
      td {
        padding: rem(8px);
      }
    }

    table,
    tr,
    td {
      border-color: $light-gray; // Fix FF border color issue.
    }
  }
}

.probe_view_compatible_table {
  
  a.view-hide-trigger {
    text-decoration: none;
    
    &:after {
      content: "\7c";
      font-family: "tek";
      margin-left: rem(5px);
      font-size: 66%;
    }
    
    &.opened:after {
      content: "\7d";
      font-family: "tek";
      margin-left: rem(5px);
      font-size: 66%;
    }
  }
}

.striped .probe__table table {
  border: 0;
  
  tr:nth-child(even) {
    background-color: $white;
  }
  
  tr:nth-child(odd) {
    background-color: $gray-f5;
  }
}
