/**
 * @file
 * Styles for Redesigned Product Series.
 *
 * Use D6 markup.
 *
 */

.product-series-redesign {
  .tab-pane {
    .item {
      @extend %padding-v;

      &.grey {
        @extend %out-of-container;

        &:before {
          background-color: $gray-f5;
        }
      }

      .img-wrap {
        img {
          @extend %center-block;
        }
      }

      .text-wrap {
        ul {
          @extend %list--blue-points;
        }
      }

      .link-wrap {
        margin: rem(25px) 0;

        .inline {
          margin-right: rem(20px);
        }
      }

      h3 {
        @extend %title;
        margin-top: 0;
      }
    }

    .inner-wrap-item {
      @extend %clearfix;
    }

    .title-text {
      @include helvetica-arial-bold;
      color: $black;
    }

    @include breakpoint($mobile-only) {
      .title {
        text-align: center;
      }

      .img-wrap {
        margin-bottom: rem(20px);
      }

      .text-wrap,
      .title {
        padding: 0 rem($grid-gutter-width / 2);
      }
    }
  }

  @include breakpoint($tablet) {
    .img-wrap,
    .text-wrap,
    .left-img .title,
    .right-img .title {
      @include make-md-column(6);
    }

    .left-img {
      .title {
        float: right;
      }
    }

    .right-img {
      .img-wrap {
        float: right;
      }
    }
  }
  // Add corresponding icons to classes from migrated content.
  .action,
  .read,
  .see-now,
  .see-it,
  .glasses {
    @extend %left-icon;
  }

  .action,
  .see-now,
  .see-it {
    @extend .icon-play-new;
  }

  .read {
    @extend .icon-read;
  }

  .glasses {
    @extend .icon-glasses;
  }
}

.product-series {
  overflow-x: hidden;
}
