.manual-filter {

   &__container{
   background-color:$grey-bg;
   padding: rem(50px) 0 rem(40px) 0;
   }

   &__row{
   margin-bottom: rem(20px);
   margin-left: rem(20px);
   width:80%;
   float:left;
   }

   &__button{
   margin-left: rem(50px);
   }

   .form-item-product-series, .form-item-product, .form-item-manual-type {
   	
   	label {
   	color:#6d6e69;
   	}

   }
}
 
thead .views-field-field-manual-type, .views-field-field-release-date {
	color:$black;
		a {
			color:$black;
			font-weight: bold;
		}
}

.view--page-manual-downloads .views-field-field-manual-type {
	border-bottom: 1px solid $gray-c;
  color:$black;
  background-color: $light-grey-e;
  padding-left: rem(20px);
	strong {
	  font-weight:bold;
	}
}

tbody .views-field-field-release-date {
	border-bottom: 1px solid #ccc;
	background-color:#ddd;
    padding-left: rem(20px);
    padding-right: rem(20px);
}
