/**
 * @file
 * sass/partials/components/_document.scss
 *
 */

.doc {
  &__img {
    img {
      margin: 0 auto rem(20px);
    }
  }

  &--related {
    legend {
      @include  helvetica-arial-bold;
    }

    .fieldset-wrapper {
      .field {
        margin-bottom: rem(10px);
      }
    }

    .field--title {
      @include helvetica-arial-book;
      color: $lightest-grey;
      margin-right: rem(5px);
    }

    .field--content {
      a {
        @include helvetica-arial-light;
        margin-right: rem(5px);

        &:after {
          content: '\002C';
          color: $base-font-color;
        }

        &:last-child {
          &:after {
            content: none;
          }
        }
      }
    }

    .date {
      color: $light-grey;
      font-size: rem(9px);
    }
  }

  @include breakpoint($mobile-landscape) {
    &__img {
      float: right;
      img {
        margin: 0 0 rem(20px) rem(20px);
      }
    }

    &--related {
      .field--title {
        min-width: rem(115px);
      }

      .fieldset-wrapper {
        .field {
          @include display(flex);
        }
      }
    }
  }
}
