.orderstatus-form {
  padding: rem(10px) 0;


    label {
      font-size: rem(18px);
      @include helvetica-arial-bold;
    }

    input, select {
      @include helvetica-arial-light;
      color: $grey-5;
    }

    .form-item, .form-action{
        
        padding-bottom: rem(15px);
        .description {
          @include helvetica-arial-light;
          font-size: rem(14px);
          color: $grey-5;
        }
    }

    .form-submit {
      color: $white;
      font-weight: 700;
    }

 
  
  @include breakpoint($tablet) {

    input {
      width: calc(100% / 3);
    }

    input.form-submit {
      width: initial;
    }

    select {
      width: calc(100% / 5);
    }

  }

}

.order-status__summary, .order-status__details {

  table {
    table-layout: auto;
    border-collapse: collapse;

    th, td {
      padding: rem(8px);
    }

    tbody>tr:nth-of-type(odd) {
      background: $grey-f9; 
    }

    tbody>tr:nth-of-type(even){
      background: $light-grey-e;
    }
  }

  table, th, td {
    border: 1px solid $light-gray;
  }

}
