.product-card {
 @extend %margin-bottom;

  h3 {
    @include grotesk-regular;
    font-size: rem(20px);
    margin-bottom: rem(10px);
  }

  & > a {
    text-decoration: none;

    &:hover {
      h3 {
        text-decoration: underline;
      }
    }
  }

  &__image {
    font-size: 0;
  }

  &__text {
    &,
    p {
      color: $base-font-color;
      font-size: rem(14px);
      line-height: rem(22px);
    }
  }

  @include breakpoint($mobile-landscape) {
    &__image {
      @include display(flex);
      @include align-items(center);// Align vertically images with different height.
      min-height: rem(206px); // For image styles with scale height (206px) effect.
    }
  }
}

// Related product card.

.product--related {
  font-size: rem(14px);

  a {
    @extend %scale-img;
    text-decoration: none;

    h3 {
      color: $light-grey;
      font-size: rem(22px);
    }

    &:hover {
      h3 {
        color: $secondary-blue2;
      }
    }
  }

  &__image {
    overflow: hidden;
    margin-bottom: rem(20px);
  }

  @include breakpoint($mobile-landscape) {
    margin-bottom: rem(20px);
    padding-top: rem(20px);

    h3 {
      min-height: rem(52px); // Title comes max in two lines.
    }
  }
}

// Advert card.
.advert-card {
  margin-bottom: rem(20px);

  h4 {
    @include helvetica-arial-book;
  }

  &__image {
    margin-bottom: rem(20px);
  }
}
