.no-banner-container {
    
    @include breakpoint ($tablet) {
        margin: rem(50px) 0;
    }
}

.banner.banner--hero.title ~ .field--field_content_container:nth-child(2) {
    @include breakpoint ($tablet) {
        position: relative;
        margin-top: rem(-100px);
    }
}

.content-block { 
    .container {
        padding: rem(20px);
    }
    
    &.bottom {
        @include display(flex);
        @include flex-direction(column);
        
        .content-block__full-image {
            @include order(2);
        }
    }
    
    &__full-image {
        padding: rem(20px) 0;
        text-align: center;
        
        img {
            display: inline-block;
        }
    }
    
    h3 {
        font-size: rem(35px);
    }
    
    &.banner-container {
        margin: rem(10px) 0 0 0;
        h3 {
            margin-top:0;
        }
        @include breakpoint($desktop){
            margin: rem(40px) 0 0 0;
        }
        @include breakpoint($mobile-only){
            .color--white {
                color: $grey-3;
            }
            
        }
    }
    
    &__copy {
        
        & .bolded  {
            font-size: rem(22px);
            font-weight: 400;
            padding-left: 30px;
            border-left: 2px solid $dark-blue3;
        }
        
        img {
            padding: rem(10px) 0;
        }
        
        @include breakpoint($mobile-only){
            p {
                color: $grey-3;
            }
            img {
                padding: rem(20px) 0;
            }
        }
        
        @include breakpoint ($tablet) {
            &.two-column {
                column-count: 2;
                column-gap: rem(40px);
            }
            
            &.three-column {
                column-count: 3;
                column-gap: rem(40px);
            }
            
            &.four-column {
                column-count: 4;
                column-gap: rem(40px);
            }
        }
    }
    
    @include breakpoint ($tablet) {
        margin: rem(50px) 0;
    }
}
