.sc-hide {
  display: none;
}

.sc-button {
  background-color: $mid-green;
  border-color: #b5c79d #506e2b #506e2b #b5c79d;
  border-radius: rem(4px);
  border-right: rem(1px) solid #506e2b;
  border-style: solid;
  border-width: rem(1px);
  color: $white;
  font-weight: bold;
  padding: rem(3px) rem(5px);
  cursor: pointer;
}

#sc-topbar {
  background-color: $gray-c;
  color: $white;
  border-right: rem(1px) solid $white;
  text-align: right;
  padding: rem(3px) rem(10px);
  font-size: rem(11px);
  width: 100%;
  margin-bottom: rem(8px);
}

#sc-topbar a, #sc-topbar a:visited, #sc-phone-chat {
  color: $black;
}

#sc-phone-chat {
  padding-right: rem(12px);
}

#sc-chat {
  text-decoration: underline;
}

#sc-print-email .button2ln {
  cursor: pointer;
}

#sc-container {
  padding: 0 0 rem(30px) 0;
  ul {
    list-style: none;
    padding-left: 0;
    margin-left: rem(15px)
  }
  .sc-standard-name {
    font-size: rem(14px);
    font-weight: 700;
    margin-bottom: 0;
  }
  #add-selection {
    margin-left: rem(2px);
  }
  #sc-print-email {
    .button2ln {
      color: $white;
      font-size: rem(12px);
      font-weight: bold;
      line-height: 1.2em;
      padding: rem(2px) rem(30px) rem(10px) rem(10px);
      text-transform: uppercase;
    }

    p {
      margin: 0;
    }

  }

  &.sc-table #sc-results {
    width: 100%;
  }

  &.sc-table .sc-action {
    float: left;
    margin-right: rem(20px);
  }

  #sc-results span {
    font-size: rem(10px);
    color: $medium-gray;
    cursor: pointer;
    font-weight: 400;
  }

  #sc-results span:hover {
    text-decoration: underline;
    color: $blue-hover;
  }
  
  #sc-send {
    margin-top: rem(10px);
  }

  #sc-email-msg {
    color: red;
    display: block;
    clear: both
  }

  #sc-selection p {
    padding: rem(15px) rem(15px) 0 0
  }

  #sc-selection ul {
    padding-left: 0;
    margin-left: rem(2px)
  }

  #sc-results {
    border-left: 1px solid #ddd;
    @include breakpoint($phone-only) {
      margin-top: rem(15px);
      padding-top: rem(15px);
      border-left:none;
      border-top: 1px solid #ddd;
    }
  }

  #sc-controls ul {
    margin-left: 0;
  }

}

#sc-container .sc-dut-name,
.sc-app-name {
  margin: 0;
  color: $dark-gray;
  font-size: rem(11px);
}

#config-holder {
  padding-bottom: rem(12px);
}

#sc-results {
  .result {
    padding-bottom: rem(12px);
  }
  table {
    thead {
      th {
        font-weight: bold;
      }
    }
  }
}

#no-applications {
  padding-top: rem(12px);
}

#description-label-left {
  font-weight: bold;
}

#sc-email-form span,
#sc-email-form input {
  *float: left;
}

.tek-serial-conf-form {
  .form-item {
    margin-bottom: rem(10px);
  }
}
