.refurbished-item-tile {
  @include helvetica-arial-light;


  h4 {
    @include helvetica-arial-book;
  }

  ul {
    padding: 0 0 0 1.2rem;
    
  }
  li {
  padding-bottom:200px;
  }
  &_btn{
    margin: rem(15px) rem(0px) rem(5px) rem(30px);
  }
  &__img {
    padding: rem(10px) 0 0 0;

    img {
      margin: 0;
    }
    & a {
    color:$orange !important;
    }
  }
  &_noPadding {
  padding:rem(0px) !important;
  }

  

  @include breakpoint($tablet) {
    padding: 0 0 rem(20px) 0;

    &__img {
      float: right;
      padding-left: rem(10px);
    }
  }
}
