.courseware-filter {
    border-top: rem(1px) solid $gray;
    background: $gray-f0;
    padding: rem(10px) rem(40px);
    margin-bottom: rem(25px);
    @include breakpoint($tablet){
      padding: rem(20px) rem(20px);
    }
    overflow: hidden;

    .form-item {
      padding: rem(10px);

      @include breakpoint($tablet){
        width: 30%;
        float: left;
      }
    }
    .description {
      font-size: rem(12px);
    }

  &__button {
    float: left;
  
    @include breakpoint($tablet) {
      padding-top: rem(35px);
    }
  }
}

.view--my-courseware {
  
  .views-field-title a {
    font-size: rem(18px)
  }

  .views-label-langcode {
    font-weight: 500;
  }
  
  .button-courseware a {
    margin: rem(5px) 0 rem(10px) 0; 
  }
}

.node--lab_course {
  .courseware-download-wrapper {
    margin-top: 2em;
    }
  .description-policy {
      font-size: rem(12px);
    }
  .field--field_description .field--comment__title {
    margin-top: 0;
    }
  h2.node__title {
    color: #666;
    }
}
