.recommended-equip-tile {
  @include helvetica-arial-light;

  h4 {
    @include helvetica-arial-book;
  }

  &__img {
    padding-bottom: rem(10px);

    img {
      margin: 0;
    }
  }

  @include breakpoint($tablet) {
    padding: 0 0 rem(20px) 0;

    &__img {
      float: right;
      padding-left: rem(10px);
    }
  }
}

// Application CT.
.field--field_app_recommended_equipment {
  h4 {
    @include helvetica-arial-book;
  }

  .embedded-entity img {
    float: right;
    width: rem(180px);
    padding: 0 0 rem(5px) rem(5px);

    @include breakpoint($mobile-landscape-tablet-only) {
      float: none;
      width: auto;
      padding: 0 0 rem(5px);
    }
  }
}

.recommended-equip-item {
  padding: rem(20px);

  &__description {
    padding-top: rem(20px);
  }

  ul {
    line-height:1.6;
    margin-bottom:0;
    &:before {
      content: '';
      display: block;
      margin: rem(20px) 0 rem(20px) rem(-40px);
      width: rem(75px);
      height: rem(3px);
      @include gradient-horizontal ($vibrant-blue, $mid-green);
    }
  }

  .btn {
    margin-top: rem(20px);
  }
}
