.sales-tool--browse {
  padding-bottom: rem(30px);
  border-bottom: 0.0625rem solid $grey-e5;

  h2 {
    text-align: center;
    font-weight: 600;

    &:nth-child(3) {
      margin-bottom: 0;
    }
  }

  ul {
    list-style-type: none;
    padding: 0;
    text-align: center;

    li {
      display: inline-block;
      width: 100%;

      a {
        margin: rem(10px) 0;
        width: 100%;
      }
    }

    @include breakpoint($mobile-landscape) {
      padding: rem(20px) 0 0 0;

      li {
        width: auto;

        a {
          margin: rem(10px) rem(20px);
          width: rem(250px);
        }
      }
    }
  }

  &-search.search-page-box {
    border: none;
  }
}

.sales-tool--column {
  text-align: center;

  @include breakpoint($mobile-landscape) {
    &:nth-child(3n+0) {
      clear: both;
    }
    &:nth-child(4n+0) {
      clear: none;
    }
  }

  @include breakpoint($tablet) {
    &:nth-child(4n+0) {
      clear: both;
    }

    &:nth-child(3n+0) {
      clear: none;
    }
  }

  h3 {
    font-weight: 600;
    font-size: rem(20px);
    margin: rem(30px) 0;
  }

  ul {
    list-style-type: none;
    text-align: left;
    margin: 0;
    padding: 0;

    li {
      padding: rem(5px) 0;
      font-size: rem(14px);
      font-weight: 500;
      border-bottom: 0.0625rem solid $grey-e5;

      &:last-of-type {
        border: none;
      }
    }
  }
}
