// Blog styles.

.blog-featured {
  @extend %clearfix;

  header {
    h2 {
      color: $base-font-color;
      font-size: rem(24px);
    }

    .btn {
      cursor: default;
      font-size: rem(10px);
      padding: rem(3px) rem(8px);
      margin-right: rem(10px);
    }
  }

  &__img {
    margin-bottom: rem(20px);
  }

  @include breakpoint($mobile-landscape) {
    &__img {
      float: left;
      margin: 0 rem(15px) rem(10px) 0;
      width: 30%;
    }
  }

  @include breakpoint($tablet) {
    &__img {
      margin: 0 rem(40px) rem(20px) 0;
      width: 50%;
    }
  }
}

// Featured unified styles, applied for featured blog, featured video.
.featured {
  color: $lightest-grey;
  font-size: rem(14px);
  padding: rem(20px) 0;

  header {
    @include helvetica-arial-light;
    margin-bottom: rem(20px);
  }

  h2 {
    margin-top: 0;

    a {
      @extend %featured-title;
    }
  }

  .date {
    @extend %separator--right;
  }

  .label {
    @include helvetica-arial-bold;
    margin-right: rem(10px);
  }
}

.blogger {
  @extend %clearfix;
  @include helvetica-arial-light;
  border-bottom: $base-border;
  color: $medium-grey-7f;
  font-size: rem(14px);
  padding: rem($grid-gutter-width/2) 0;

  &__img {
    max-width: rem(65px);
    float: left;
    margin: 0 rem(10px) rem(10px) 0;
  }

  .label {
    @include helvetica-arial-bold;
  }

  p {
    margin: 0;
  }
}

// Blog Teaser Styles

.blog-teaser {
  @extend %clearfix;
  @include helvetica-arial-light;
  font-size: rem(13px);
  padding: rem(25px) 0;
  border-bottom: $base-border;

  &__title {
    @include helvetica-arial-light;
    display: block;
    color: $base-font-color;
    font-size: $base-font;
    margin-bottom: rem(5px);
  }

  header {
    margin-bottom: rem(20px);

    .date {
      @extend %separator--right;
    }

    .label-by {
      color: $lightest-grey;
    }
  }

  &__img {
    margin-bottom: rem($grid-gutter-width/2);
  }

  &__description {
    clear: both;
  }

  @include breakpoint($mobile-landscape) {
    &__img {
      float: left;
      margin-right: rem($grid-gutter-width/2);
      width: 30%;
    }
  }
}

.blog-author {
  color: $tk-blue;
}

.action-links {
  @extend %clearfix;
  @include helvetica-arial-light;
  left: 0;
  position: relative;

  li {
    @extend %separator--right;
    float: left;
  }

  .show-social & {
    left: -100%;
  }
}

.link-share {
  @include helvetica-arial-book;
  color: $grey-light-border;
  font-size: rem(11px);
  position: relative;
  padding-left: rem(25px);
  text-transform: uppercase;

  &:after {
    content: none;
  }

  &:before {
    @extend %icons;
    @extend .icon-share;
    font-size: rem(20px);
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: rem(-10px);
  }
}

.action-links,
.share-links {
  @extend %reset-list;
  @include transition(all 0.5s ease-out);
  float: left;

  li {
    padding: rem(5px) 0;
  }
}

.share-links {
  font-size: 0;
  left: 100%;
  min-width: rem(205px); // Used in animation effect.
  position: absolute;
  padding-right: rem(24px); // Icon close icon's width.
  opacity: 0;

  li {
    float: left;
    margin-right: rem(20px);

    a {
      color: $grey-light-border;
      text-decoration: none;

      &:before {
        font-size: rem(16px);
      }
    }

    .icon-mail:hover {
      color: $link-hover;
    }

    .icon-google-plus:hover {
      color: red;
    }

    .icon-tw:hover {
      color: $tw;
    }

    .icon-in:hover {
      color: $in;
    }

    .icon-fb:hover {
      color: $fb;
    }
  }

  .icon-close {
    position: absolute;
    padding: 0;
    right: 0;
    top: 50%;
    margin-top: rem(-12px); // Half close icon's height.
    margin-right: 0;

    &:before {
      display: block;
      border-radius: 100%;
      background-color: $grey-e3;
      color: $lightest-grey;
      cursor: pointer;
      font-size: rem(10px);
      font-weight: bold;
      padding: rem(7px);
    }
  }

  .show-social & {
    left: 0;
    opacity: 1;
  }
}

.show-social {
  min-width: rem(205px);
}

.action-lists {
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.node--blog_entry {
  @include breakpoint($tablet) {
    .featured {
      @include display(flex);

      .blog-featured__img, header {
        @include flex(1);
      }
    }
  }
}

// Comments Section Styles
.comments {
  border-top: rem(1px) solid $gray;
  margin-top: rem(16px);

  &__content {
    display: block;

    &__subject {
      font-size: rem(18px);
      display: inline-block;
    }

    &__date {
      display: inline-block;
      font-size: rem(14px);
    }

    &__author {
      display: inline-block;
      font-size: rem(14px);
    }

    &__subtext {
      display: block;
      font-size: rem(12px);
      margin-top: rem(10px);

      p {
        color: $gray;
        display: inline-block;
        font-size: rem(12px);
        margin-right: rem(5px);
        margin-bottom: rem(5px);
      }
    }

    &__comment {
      display: block;

      p {
        margin-bottom: rem(5px);
      }

      ul {
        margin-bottom: rem(20px);

        li {
          display: inline-block;
          font-size: rem(12px);
          text-transform: lowercase;

          @extend %separator--right;
        }
      }
    }
  }
}
