.side-nav__lang-selector {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 9999;
  top: 0;
  right: 0;
  background-color: $deep-charcoal;
  overflow-x: hidden;
  transition: 0.5s;
  
  
  &--header {
    @include display(flex); 
    padding: 0 rem(20px) 0 0;
    cursor: pointer;
    h3 {
      text-transform: uppercase;
      font-size: rem(15px);
      color: $white; 
      padding: rem(8px) rem(11px);
      
      &.icon-chevron-right:before {
        padding-right: rem(5px);
      }
    }
    
    .icon-earth.header-right__item {
      position: absolute;
      top: 0;
      right: rem(25px);
      font-size: rem(36px);
      padding-top: 5px;
    }
    
    span.region-text {
      font-size: rem(15px);
      float: right;
      padding-top: rem(15px);
      padding-left: rem(5px);
    }
    
  }
  
  nav > ul {
    @extend %reset-list;
    padding-bottom: rem(20px);
    border-bottom: 1px solid $white;
    margin: 0 rem(20px);

     > li > a {
        @include grotesk-regular;
        color: $vibrant-blue;
        display: block;
        font-size: rem(14px);
        margin-top: rem(20px);
        text-transform: uppercase;
      }
      
      ul {
        @extend %reset-list;
        display: none;
        li {
          padding-top: rem(5px);
        }
        a {
          color: $white;
          font-size: rem(14px);
          letter-spacing: rem(.3px);
          padding: 0 0 0 rem(10px);
        }
      }
  }
  
  .contact-text {
    color: $white;
    font-size: rem(12px);
    padding-top: rem(20px);
    margin: 0 rem(20px);
  }
  
  .btn {
    margin: rem(20px);
  }
  
  @media screen and (max-height: 450px) {
    padding-top: 15px;
    a {
      font-size: rem(18px);
    }
   }

}

 

 



