.overview-section {
  @extend %margin-bottom;
  @extend %padding-top;

  &__links {
    @extend %padding-top;
    @include display(flex);
    @include justify-content(space-between);

    & > .top-icon,
    & > div {
      @include flex(1);
      text-align: center;
    }

    .top-icon {
      &:before {
        padding: rem(10px);
      }
    }

    img {
      @include transform(translateX(-50%));
      height: rem(70px);
      position: absolute;
      left: 50%;
      top: 0;
      width: auto;
      z-index: -1;
    }

    .icon-play-filled {
      // Color video triangle in white.
      &:after {
        @include transform(translateX(-50%));
        content: '';
        background-color: $white;
        border-radius: 50%;
        height: rem(40px);
        width: rem(40px);
        left: 50%;
        top: rem(11px);
        position: absolute;
        z-index: -1;
      }
    }

    .ajax-progress {
      position: absolute;
      left: 50%;
      top: 100%;
    }
  }
  
  @include breakpoint($mobile-only) {
    h1 {
      text-align: center;
    }
  }

  @include breakpoint($desktop) {
    h1 {
      font-size: rem(55px);
      line-height: rem(60px);
    }
  }
}
