.prod-warranty {
  padding: rem(10px) 0;

  &__label {
    @include helvetica-arial-bold;
  }

  &__form {
    @include helvetica-arial-light;
  }

  form {
    label {
      @include helvetica-arial-light;
      font-size: rem(11px);
    }

    input {
      @include helvetica-arial-light;
      color: $grey-5;
    }

    ul {
      @extend %reset-list;

      ul {
        font-size: rem(14px);
        list-style-type: circle;
        padding-left: rem(25px);
      }
    }

    .form-submit {
      color: $white;
      height: rem(35px);
    }
  }
  
  @include breakpoint($mobile-only) {
    padding: rem(15px);

    &__label {
      @include sr-only;
    }

    .form-item,
    .form-actions {
      margin-bottom: rem(15px);
    }
  }

  @include breakpoint($tablet) {
    border: rem(1px) solid $light-gray;

    &__label,
    &__form form {
      @include display(flex);
      @include align-items(flex-end);

      & > div {
        padding: rem(8px);
        width: calc(100% / 4);
      }
    }

    &__label {
      border-bottom: rem(2px) solid $light-gray;
    }
  }

  @include breakpoint($desktop) {
    &__label,
    &__form form {
      & > div {
        padding-left: rem(20px);
        padding-right: rem(20px);
      }
    }
  }
}
