.info-wedge {
  
  padding: rem(30px);
  
  &::after {
    clear: both;
    content: "";
    display: table;
  }
  
  &__content {
    @include display(flex);
    @include flex-flow(row wrap);
  }
  
  .Left {
    @include order(1);
  }
  
  .Right {
    @include order(2);
  }
  
  &.bg-color--grey {
    background-color: $gray;
  }
  &.bg-color--light-grey {
    background-color: $gray-f5;
  }
  &.bg-color--blue {
    background-color: $tk-blue;
    h2, p, ul li {
     color: $white;
    }
  }
  &__header {
    padding-left: rem(15px);
    padding-right: rem(15px);
    &.col-sm-push-6 {
     padding-left: 0;
     padding-right: 0;
    }
  }
  h2 {
    color: #006281;
    font-size: rem(28px);
    padding-bottom: rem(20px);
    margin: 0.625rem 0 0 0;
  }
  
  p {
    font-size: rem(16px);
    line-height: rem(25px);
  }
  
  a {
    font-weight: 400;
    font-size: rem(15px);
  }
  
}
