.about-us-menu {
  padding: rem(25px) 0;

  ul {
    @extend %clearfix;
    @extend %reset-list;
  }

  li {
    @extend %separator--right;
    padding: rem(5px) 0;
    float: left;

    a {
      @include helvetica-arial-regular;
      color: $medium-grey;
      font-size: rem(11px);
      text-transform: uppercase;
      text-decoration: none;

      &.is-active {
        position: relative;

        &:before {
          @extend %icons;
          @extend .icon-chevron-down;
          font-size: rem(12px);
          margin-top: rem(3px);
          position: absolute;
          top: 100%;
          left: 50%;
          margin-left: rem(-6px); // Half icon size.
        }
      }

      &.is-active,
      &:hover {
        color: $tk-blue;
      }
    }
  }

  @include breakpoint($mobile-landscape) {
    li a {
      margin: 0 rem(15px);
    }
  }
}
