/**
 * @file
 * All tabs styles go here.
 */

$tabs-width: rem(170px);
// Tek tabs.

.tek-tabs__wrapper {
   clear:both;
}

.tek-tabs, .learning-tabs {
  @extend %clearfix;

  .tab-pane {
    @extend %clearfix;
    background-color: $gray-f5;
    border: rem(1px) solid $grey-e5;
    padding: rem(20px);
  }

  @include breakpoint($tablet) {
    &.vertical-tabs {
      .tek-tabs__content {
        padding-left: calc(#{$tabs-width} - 1px);
      }

      // Fix vertical gap from 'clear:both' rule.
      .tab-pane > div {
        overflow: auto;
      }
    }
  }

}

.tab-pane {
  .tek-tabs &,
  .sticky-tabs &,
  .learning-tabs & {
    word-wrap: break-word;
    word-break: break-word;
  }
}

.blog-tabs {
  .tab-pane {
    background: none;
    border: none;
    padding-top: 0;
    padding-bottom: 0;

    @include breakpoint($tablet) {
      display: block;
    }

    @include breakpoint($desktop) {
      h3 {
        font-size: rem(28px);
      }

      &.col-md-4 {
        padding-left: rem(40px);
      }
    }
  }
}

.tek-tabs__nav, .learning-tabs__nav {
  li {
    width: 100%;
    margin-bottom: rem(1px);
  }

  a {
    color: $black;
    display: block;
    font-size: rem(11px);
    padding: rem(15px);
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;

    .blog-tabs & {
      background-color: $white;
      border: none;
    }

    &.active {
      background-color: $gray-f5;
      color: $tk-blue;
      border: rem(1px) solid $grey-e5;
      border-top: rem(2px) solid $tk-blue;

      .blog-tabs & {
        border-bottom: 0;
      }
    }
  }

  .blog-tabs & {
    @include display(flex);
    border-bottom: rem(2px) solid $light-gray;

    li {
      width: 50%;
      margin-bottom: rem(-2px);
    }
  }

  @include breakpoint($mobile-only) {
    li {
      a {
        background-color: $grey-e3;
        border: rem(1px) solid $grey-e5;
      }
    }
  }

  @include breakpoint($tablet) {
    @include display(flex);
    position: relative;
    z-index: 2;

    li {
      width: $tabs-width;
      margin: 0 rem(1px) rem(-1px) 0;
    }

    a {
      height: 100%;
      text-align: center;

      &.active {
        border-bottom-color: $gray-f5;
      }
    }

    .vertical-tabs & {
      @include flex-direction(column);
      float: left;

      li {
        margin: 0 0 1px 0;
      }

      a {
        background: $grey-e3;

        &.active {
          background-color: $gray-f5;
          border: rem(1px) solid $grey-e5;
          border-left: rem(2px) solid $tk-blue;
          border-right: 0;
          font-weight: bold;
        }
      }
    }

    .blog-tabs & {
      display: none;
    }
  }
}

.blog-tabs {
  clear: both;

  @include breakpoint($mobile-only) {
    padding-top: rem(20px);
  }
}

.tab-pane {
  display: none;

  &.active {
    display: block;
  }
}

// admin tabs
.block--local-tasks {
  ul {
    @extend %clearfix;
    @extend %reset-list;
    border-bottom: rem(2px) solid $grey-e5;
    padding: rem(20px) rem(20px) 0;
    margin-bottom: rem(20px);
  }

  &__task {
      float: left;
      margin-bottom: rem(-2px);

      a {
      color: $light-grey;
      display: block;
      font-size: rem(12px);
      padding: rem(10px);
      text-transform: uppercase;

      &:hover,
      &:focus,
      &.is-active {
        color: $link-hover;
      }

      &.is-active {
        background-color: $gray-f5;
        border: rem(1px) solid $grey-e5;
        border-top: rem(2px) solid $dark-blue;
        border-bottom: 0;
      }
    }
  }

  @include breakpoint($tablet) {
    &__task {
      a {
        padding: rem(10px) rem(20px);
      }
    }
  }
}

// Product Series demo tabs
.demo-tabs {
  @include display(flex);

  &.reset-list {
    padding: rem(20px) 0;
  }

  li {
    @include flex-grow(1);
    @include flex-basis(0);
    float: left;
    border: rem(1px) solid $light-gray;
    border-left-width: 0;
    position: relative;

    &:first-child {
      border-left-width: rem(1px);
    }

    .ajax-progress-throbber {
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  a {
    color: $black;
    display: block;
    font-size: rem(12px);
    outline: none;
    padding: rem(30px) 0;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;

    &:before {
      color: $secondary-blue2;
      display: inline-block;
      font-size: rem(24px);
      margin-right: rem(5px);
      vertical-align: middle;
    }
  }
}

// Country selector tabs.

.country-wrapper {
  .region-tab {
    li {
      a {
        color: $black;
        display: block;
        font-size: rem(12px);
        padding: rem(12px) 0;
      }

      @include breakpoint($mobile-landscape) {
        @include make-sm-column(6);
      }

      @include breakpoint($tablet) {
        @include make-md-column(4);
      }
    }
  }
}

// Sticky tabs.
.sticky-tabs {
  @extend %row-sm;

  &__head {
    @extend %margin-bottom;
    height: rem(45px); // Sticky nav height.
    position: relative;
    z-index: 21;

    .dropdown {
      @include transition(opacity 0.3s ease-out);
      display: none;
      opacity: 0;
      position: absolute;
      right: rem($grid-gutter-width / 2);
      top: rem(7px);
    }

    header {
      @extend %out-of-container;

      &:before {
        box-shadow: 0 3px 2px -2px $light-gray;
      }
    }

    .is-sticky & {
      header {
        background-color: $white;
        left: 0;
        position: fixed;
        margin-top: rem($headerHeight); // Site header height.
        top: 0;
        width: 100%;
      }

      .dropdown {
        display: block;
        opacity: 1;
      }
    }
  }

  &__title,
  &__nav a {
    line-height: 1;
    padding: $grid-gutter-width / 2;
  }

  &__title {
    @include helvetica-arial-book;
    font-size: rem(15px);
    position: relative;

    &:before {
      @extend %icons;
      @extend .icon-chevron-down;
      @include transform(translate(-50%,-50%));
      color: $vibrant-blue;
      font-size: rem(26px);
      font-weight: bold;
      left: 50%;
      position: absolute;
      top: 50%;
    }
  }

  &__nav {
    a {
      @include helvetica-arial-regular;
      display: block;
      text-align: center;

      &.active {
        color: $black;
      }
      
      &:hover {
        text-decoration: none;
      }
    }
  }

  @include breakpoint($mobile-only) {
    &__title {
      cursor: pointer;
      min-height: rem(47px);
    }

    &__nav {
      @include transition(all 0.3s ease-out);
      background-color: $gray-f5;
      max-height: 0;
      overflow: hidden;

      .is-opened & {
        border-top: rem(1px) solid $gray-c;
        max-height: rem(1000px);
      }

      a {
        border-bottom: rem(1px) solid $gray-c;
        border-top: rem(1px) solid $white;

        &.active {
          color: $black;
        }
      }
    }

    .tab-pane > h3 {
      padding: 0 rem($grid-gutter-width / 2);
    }
  }

  @include breakpoint($tablet) {
    &__title,
    &__nav li {
      float: left;
    }

    &__title {
      margin-right: rem(30px);
      padding-left: 0;

      &:before {
        content: none;
      }

      .is-sticky & {
        display: none; // Make space to fit nav items for small desktop.
      }
    }

    &__nav {
      @include clearfix();

      a {
        font-size: rem(13px);
        padding: rem(15px) 0;
        margin: 0 rem(8px);

        &.active {
          @include colored-border;
        }
      }
    }

    &__head {
      header {
        // Move the sticky header under toolbar.
        .toolbar-fixed &,
        .toolbar-vertical & {
          .is-sticky & {
            margin-top: rem($toolbarTabHeight + $headerHeight); // toolbar-tab plus site header height;
          }
        }

        .toolbar-tray-open.toolbar-horizontal & {
          .is-sticky & {
            margin-top: rem($toolbarTabHeight + $toolbarMenuHeight + $headerHeight); // toolbar-tab plus toolbar-menu plus site header height;
          }
        }
      }

    }
  }

  @include breakpoint($desktop) {
    &__nav {
      a {
        font-size: rem(15px);
        margin: 0 rem(15px);

        &.active {
          @include colored-border;
        }
      }
    }
  }

  @include breakpoint($desktop-wide) {
    .is-sticky & {
      .sticky-container {
        @include container-fixed;
        position: relative;
        width: $container-xl;
      }

      .sticky-tabs__title {
        display: block;
      }
    }
  }
}

.learning-tabs {
  &__nav {
    li {
      transition: opacity 300ms;
      &:nth-of-type(1) {
        background-color: $dark-gray;
      }
      &:nth-of-type(2) {
        background-color: $tk-blue;
      }
      &:nth-of-type(3) {
        background-color: $mid-green;
      }
      a {
        color: $white;
        background-color: initial;
        &.active {
          color: $white;
          background-color: initial;
          border-top: none;
          border-bottom: none;
        }
      }
    }
    &:hover li:not(:hover) {
      opacity: .6;
    }
  }
  &__content {
    > .tab-pane {
      
      &:nth-child(3){
        border-top: rem(7px) solid $mid-green;
        padding-top: rem(10px);
      }
    }
    .tab-pane {
      background: none;
      border: none;
      padding: 0;
      &:nth-child(1) .tek-tabs__nav  {
        background: $dark-gray;
      }
      &:nth-child(2) .tek-tabs__nav {
        background: $tk-blue;
      }
      &:nth-child(3) .tek-tabs__nav {
        background: $mid-green;
      } 

      @include breakpoint ($mobile-only){
        margin: rem(15px);
      }

      .tek-tabs__nav {
       @include justify-content(flex-end);
        a {
          background-color: initial;
          color: $white;
          &.active {
            color: $charcoal;
            background-color: $white;
          }
        }
      }
    }
  }
}
