.article-social-share {
	h3 {
		text-transform: uppercase; 
		text-align: center;
		color: $vibrant-blue;
		border-top: rem(3px) solid $dark-gray;
		padding-top: rem(10px);
		font-size: rem(32px);
	}

	ul {
		list-style-type: none;
		padding-left: 0;
		li {
			padding: rem(10px) 0;
   		border-top: rem(1px) solid $dark-gray;
   		display: flex;
   		flex-direction: column;
   		
   		a {
   			display: flex;
  			align-items: center;
  			justify-content: space-evenly;
  			color: $charcoal;
  			text-transform: uppercase; 

  			&:hover {
  				text-decoration: none;
  			}
   		}
		}
	}
}
