

.encore-filter {
  
  &__container {
    border-top: rem(1px) solid $gray;
    @include gradient-vertical($white, $grey-bg);
    padding: rem(20px) rem(40px);
    overflow: hidden;
    .form-submit {
        margin-top:rem(10px);
        background: #5E6A71 !important;
    }
  }
  &__title {
  		border-top:1px solid #ccc;
  		padding: rem(10px) 0 0 0 !important;
  }
  &__learn-more {
  padding-top:rem(5px);
  }

.line {
	display: none !important;
    position: absolute !important;
    width: 100% !important;
    border: 1px solid #5e6975 !important;
    height: 0 !important;
    padding: 0 !important;
    left: 0 !important;
    top: 35px !important;
    z-index: 1 !important;
}

  .form-item-region  {
    width: 35%;
    @include breakpoint($mobile-only){
      width: 100%
    }
  }


  &__separator {
    float: left;
    padding: rem(20px) rem(20px);
    @include breakpoint($mobile-only){
      clear: both;
      float: none;
      text-align: center;
    }
    @include breakpoint($tablet){
    padding-left:40%;
    }
    @include breakpoint($desktop){
    padding-left:0%;
    }
  }

  &__or {
    background: $grey-border;
    padding: 0.7rem 0.875rem;
    border-radius: rem(80px);
    color: $white;
    font-size: rem(22px);
    position: relative;
    margin: rem(5px) rem(40px);

  &::before, &::after {
       content: "";
       position: absolute;
       border: rem(1px) solid $grey-border;
     }


    @include breakpoint($mobile-tablet-only){
        &::before {
          left: -48px;
          width: 100%;
          height: 1px;
          top: 1.4375rem;
        }
    
        &::after {
          left: 48px;
          width: 100%;
          height: 1px;
          top: 1.4375rem;
        }
    }

    @include breakpoint($desktop){
      &::before {
        top: rem(-15px);
        height: 33%;
        left: rem(23px);
        width: rem(1px);
      }
  
      &::after {
        bottom: rem(-15px);
        height: 33%;
        left: rem(23px);
        width: rem(1px);
      }       
    }
  }

  &__button {
    padding-top: rem(20px);
    clear: both;
    .button {
      background: $grey-border;
      :hover {
        background: $grey-border;
      }
    }

    @include breakpoint($desktop) {
      clear:none;
      padding-top: rem(10px);
    }
  }
}
