/**
 * @file
 * sass/components/_price-spec.scss
 *
 * CSS rules that style design components (and their associated states and themes.)
 */
.price-spec {
  @extend %out-of-container;
  @extend %padding-v;

  &:before {
    @include gradient-vertical($white, $grey-bg);
    border-top: rem(1px) solid $grey-e5;
    border-bottom: rem(1px) solid $grey-e5;
  }

  .label,
  .title {
    color: $light-grey;
    font-size: rem(11px);
    margin-bottom: rem(10px);
    text-transform: uppercase;
  }

  .value,
  &__spec p {
    @include grotesk-light;
    color: $black;
    font-size: rem(14px);
  }

  &__price {
    .value {
      margin-bottom: rem(10px);
    }

    .left-icon {
      display: inline-block;
      font-size: rem(12px);
      line-height: rem(31px); //Button height.

       &:before {
         @include transform(translateY(-50%));
         top: 50%;
       }
    }

    .dropdown {
      margin: 0;
    }
  }

  &__spec {
    .inner-wrap {
      @include display(flex);
      @include justify-content(space-between);
    }

    p span {
      font-size: rem(28px);
      margin: 0 rem(5px);
    }
  }

  @include breakpoint($mobile-only) {
    &__price {
      border-bottom: rem(1px) solid $grey-e5;
      padding-bottom: rem(20px);

      .left-icon {
        float: right;
      }
    }

    &__spec {
      .inner-wrap {
        @include flex-wrap(wrap);
        width: 100%;

        .item {
          padding-top: rem(30px);
          width: 50%;
        }
      }
    }
  }

  @include breakpoint($tablet) {
    @include display(flex);
    @include align-items(flex-start);

    &:after {
      content: '';
      position: absolute;
      left: percentage((4 / $grid-columns));
      top: 0;
      width: rem(1px);
      height: 100%;
      background: $grey-e5;
    }

    &__price {
      width: percentage((4 / $grid-columns));
    }

    &__spec {
      padding-left: rem(20px);
      width: percentage((8 / $grid-columns));

      p {
        font-size: rem(16px);
        letter-spacing: rem(-0.5px);
        padding-top: rem(15px);
      }
    }
  }

  @include breakpoint($desktop) {
    .value,
    &__spec p {
      font-size: rem(18px);
    }

    &__spec {
      padding-left: rem(40px);

      p {
        span {
          font-size: rem(36px);
        }
      }
    }

    &__price {
      .left-icon {
        margin-left: $grid-gutter-width / 2;
      }
    }
  }
}
