/**
 * @file
 * Styles for Competitive Comparision section.
 *
 * Use D6 markup.
 *
 */
.comparison__side-by-side {
  @extend %padding-top;

  .title {
    text-align: center;
    h3 {
      @extend %margin-bottom;
      font-size: rem(30px);
    }
  }

  &__product-title {
    @include helvetica-arial-book;
    text-align: center;
  }

  &__product-overview {
    img {
      @extend %center-block;
    }
  }

  table {
    line-height: rem(20px);
    border: 1px solid $medium-grey-7f;
    text-align: center;

    td {
      border: rem(1px) solid $medium-grey-7f;
      padding: rem(10px);

      &:first-child,
      &:last-child {
        width: 33%;
      }
    }

    td, p {
      font-size: rem(13px);
    }

    .spec-labels {
      border-bottom: rem(1px) solid #fff;
      background-color: $table-grey;
      font-weight: bold;
      color: $white;
    }

    .last {
      border-color: $medium-grey-7f;
    }
  }

  @include breakpoint($tablet) {
    .title {
      h3 {
        font-size: rem(36px);
      }
    }

    table {
      table-layout: fixed;

      td, p {
        font-size: rem(15px);
      }
    }
  }

  @include breakpoint($mobile-only) {
    table td {
      padding: rem(5px);
    }

    .col-md-6 {
      clear: both;
    }
  }
}
