.video-teaser {
  @include helvetica-arial-light;
  border-bottom: rem(1px) solid $gray-c;
  padding: rem(24px) 0;
  
  .video-icon {
    display: inline-block;
  }

  &__text {
    &,
    p,
    label {
      font-size: rem(12px);
    }
  }
 
  &__title {
    font-size: rem(14px);
    margin-bottom: rem(5px);
  }

  &__duration-label {
    @include helvetica-arial-bold;
    display: inline;
  }

  &__description {
    margin-bottom: rem(5px);
  }

  @include breakpoint($mobile-landscape) {
    &__thumbnail {
      float: left;
      width: rem(125px);
    }

    &__text {
      margin-left: rem(140px);
    }
  }
}

.related-video {
  position: relative;

  a {
    display: block;
  }

  .video-icon {
    img {
      border: rem(1px) solid $black;
    }
  }

  h3 {
    @include helvetica-arial-light;
    font-size: rem(16px);
    line-height: 1.5;
  }

  .label {
    @include helvetica-arial-book;
  }

  .multi-line & {
    border-bottom: rem(1px) solid $gray-c;
    padding-bottom: rem(10px);
    margin-bottom: rem(24px);
  }
}

// Featured Video.

.featured-video {
  h2 {
    @include helvetica-arial-book;
  }

  h2, h3 {
    color: $base-font-color;
    font-size: rem(24px);
  }

  h3 {
    border-top: rem(1px) solid $medium-gray;
    padding-top: rem(20px);
  }

  &__media {
    @extend %responsive-video;
    margin-bottom: rem(20px);
  }

  .action-lists {
    padding-top: rem(10px);
  }
}

.youku_video:after {
  padding-bottom: 0;
}

// Media Form Styles
#views-exposed-form-media-related-videos-all-videos {
  display: none;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: $base-border;
  margin: rem(20px) 0;
  padding-bottom: rem(20px);
  transition: 1s all ease;

  & > div {
    width: 100%;
    margin: rem(10px) 0;

    @include breakpoint($tablet) {
      flex: 24%;
      margin-right: 2%;

      &:last-child {
        align-self: flex-end;
        flex: 1%;
        margin-right: 0;
      }
    }
  }

  &.show-video-filters {
    display: flex;
    transition: 1s all ease;
  }
}

.media-video {
  .view--media-related-videos {
    & > div.views-row {
      width: 100%;
      border-bottom: none;
      padding: 0;
    }
  }
}

#block-tektronix-content {
  .view--media-related-videos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    nav {
      width: 100%;
    }
  }
}

.view--media-related-videos {
  & > div.views-row {
    display: inline-block;
    width: 100%;
    border-bottom: $base-border;
    margin: rem(10px) 0;
    padding: rem(10px) 0;

    @include breakpoint($tablet) {
      width: calc(33% - 25px);
      padding: rem(20px) 0 rem(45px);
      margin: rem(10px) 0;
    }
  }
}

.browse-all-videos {
  border-bottom: $base-border;
  margin: rem(20px) 0;
  padding-bottom: 0;

  @include breakpoint($tablet) {
    padding-bottom: rem(20px);
  }

  h5 {
    text-transform: uppercase;
    font-size: rem(20px);
  }

  ul {
    text-decoration: none;
    text-align: right;
    width: 100%;
    padding: 0;

    li {
      display: block;
      text-align: center;
      margin: rem(8px) 0;
    }

    @include breakpoint($tablet) {
      li {
        display: inline-block;
        margin: 0;

        &:after {
          content: "|";
          padding: 0 rem(10px);
          color: $black;
        }

        &:last-child:after {
          content: "";
          padding: 0;
        }
      }
    }
  }
}
