.tile {
  @extend %margin-bottom;
  @extend %scale-img;

  &__img-wrap {
    a {
      display: block;
      color: $grey-3;
    }

    & > a,
    .media-image {
      overflow: hidden;
    }

    &:hover {
      a {
        color: $secondary-blue2;
      }
    }
  }

  h2 {
    @include underlined-header;
    font-size: rem(18px);
  }

  &__description {
    margin-bottom: rem(20px);
  }

  .btn-secondary {
    float: right;
  }

  &__wrapper--horizontal {
    @extend %padding-top;
    @extend %responsive-align-cards;

    .tile {
      float: none;
    }
  }

  .bg--blue {
    &, a {
      color: $white;
    }

    h2,
    .tile__description,
    .tile__links {
      padding: 0 $grid-gutter-width / 2;
    }

    h2:after {
      background-color: $white;
    }

    .btn {
      width: 100%;
      margin-bottom: rem(20px);

      &:hover {
        border-color: $white;
      }
    }
  }

  @include breakpoint($mobile-landscape) {
    h2 {
      .tile__wrapper--horizontal & {
        min-height: rem(80px); // Set 'min-height'
      }
    }

    &__description {
      .tile__wrapper--horizontal & {
        min-height: rem(75px);
      }
    }
  }

  @include breakpoint($desktop) {
    h2 {
      font-size: rem(24px);
      line-height: rem(32px);
    }
  }
  &-with-caption {
    margin: auto;

    .caption {
      font-style: italic; 
      margin-bottom: rem(20px); 
      text-align: center;
    }
  }
  &-learning {
    &__img-wrap{
      position: relative;
      display: inline-block;
    }
    &__copy-wrapper{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: rgba(28, 181, 216, 0.5);
      padding: rem(10px);
      max-height: 100%;
      height: 34%;


      h2 {
        color: $white;
        font-size: rem(18px);
        margin: rem(8px) 0;
      }

      p {
        color: $black;
        font-weight: bold;
      }

      @include breakpoint($mobile-tablet-only) {
        position: static;
        background-color: $vibrant-blue;

        h2 {
          font-size: rem(16px);
        }

        p {
          font-size: rem(14px)
        }
      }

      @include breakpoint($phone-only) {
        position: absolute;
        background-color: rgba(28, 181, 216, 0.5);
      }

      @include breakpoint($desktop) {
        position: absolute;
        background-color: rgba(28, 181, 216, 0.5);
        height: 40%;
      }
      @include breakpoint($desktop-wide) {
        height: 35%;
      }
    }
  }
}

.tile-box {
  height:rem(267px); 
  width:rem(332px) !important; 
  padding:rem(35px) !important; 
  background: url('../images/tile_box_bg.jpg') no-repeat;
  color: $white;
  margin: 0 0 rem(20px) rem(20px);

  @include breakpoint($mobile-only) {
    margin-left: 0;
  }

  h2 {
    margin-top: 0;
    color:#1CB5D8;
    font-weight:800
  }

  p {
    color: $white;
  }
}

.tofu-tile {
  
  @include breakpoint($tablet) {
    position: relative;
    top: rem(-50px);
  }
  
  &__container {
    
    @include breakpoint($tablet){
      @include display(flex);
      @include justify-content(space-between);
      @include flex-wrap(nowrap);
    }
    
    & .tile-content {
      background: $white;
      margin: rem(20px) 0;
      width: 31%;
      @include transition(background-color 750ms ease);
      
      @include breakpoint($mobile-only){
        
        &:first-child {
          margin-top: 0;
        }
        
        width: 100%;
        margin-top: rem(40px);
      }
    }
    & .tile-content:hover {
      background-color: $light-blue;
    }
  }
  
  
  &__item {
    
    & a {
      color: $deeper-charcoal;
      display: block;
    }

    & a:hover {
      text-decoration: none;
    }
    
    padding-bottom: 0;
    text-align: center;
    padding: rem(20px);
    
    & img {
      margin: 0 auto;
    }
    
    &--icon {
      padding: 0;
      margin: 0 auto;
      
      &:active, &:hover, &:focus {
        color: transparent
      }
      
      &:before {
        color: $vibrant-blue;
        font-size: rem(100px);
      }
    }
    
    &--txt {
      
      padding-top: rem(20px);
      
      h4 {
          
        //@include helvetica-arial-light;
        font-size: rem(20px);
        font-weight: 400;
        margin-bottom: 0;
        width: 80%;
        text-align: center;
        margin: 16px auto;
      }
    }
    
    [class^="icon-"] {
      padding-left: rem(22px);
      position: relative;
    }
  
    [class^="icon-"]:before, a.more-info img {
        font-size: rem(16px);
        left: 0;
        position: absolute;
        top: 0;
    }
    
  }

}

