//
// Carousel
// --------------------------------------------------


.carousel {
  position: relative;
}

.carousel-inner {
  position: relative;
  overflow: hidden;
  width: 100%;

  > .item {
    display: none;
    position: relative;
    @include transition(.6s ease-in-out left);

    // Account for jankitude on images
    > img,
    > a > img {
      line-height: 1;
      width: 100%;
      height: auto;

      @include breakpoint($tablet) {
        width: 60%;
      }

      @include breakpoint($desktop) {
        width: auto;
      }
    }
  }

  > .active {
    display: block;
  }

  > .active {
    left: 0;
  }

  .carousel-caption {
    position: relative;
    top: auto;
    width: auto;
    height: auto;
    right: auto;
    padding: rem(30px);
    overflow: hidden;

    &.pull-left {
      padding: rem(20px);
    }

     &.pull-right {
      float: left !important;
    }

    .heading {
      font-size: rem(18px);
      text-align: left;
      color: $vibrant-blue;
      margin-bottom: rem(10px);
      text-shadow: none;
    }

    .description {
      margin-bottom: rem(20px);
      word-spacing: rem(0.5px);
      font-size: rem(12px);
    }

    @include breakpoint($tablet) {
      position: absolute;
      top:0;
      width: 44%;
      height: rem(250px);
      left: 15%;
      right: 15%;
      bottom: rem(20px);
      z-index: 10;
      padding-top: rem(20px);
      padding-bottom: rem(20px);

      &.pull-left {
        right: auto;
        left: 2%;
      }

      &.pull-right {
        right: 0;
        left: auto;
      }

      .heading {
        font-size: rem(22px);
      }
    }

    @include breakpoint($desktop) {
      top: rem(20px);
      width: 35%;
      &.pull-left {
        right: auto;
        left: rem(80px);
      }

      &.pull-right {
        right: rem(45px);
        left: auto;
      }
      .heading {
        font-size: rem(28px);
      }

      .description {
        font-size: rem(14px);
      }
    }
  } 
}

.carousel-indicators {
  position: absolute;
  bottom: rem(-17px);
  left: 50%;
  z-index: 15;
  width: 60%;
  margin-left: -30%;
  padding-left: 0;
  list-style: none;
  text-align: center;

  li {
    display: inline-block;
    width:  rem(10px);
    height: rem(10px);
    margin: rem(1px);
    text-indent: rem(-999px);
    border: rem(1px) solid $medium-gray;
    border-radius: rem(10px);
    cursor: pointer;
  }

  .active {
    margin: 0;
    width:  rem(12px);
    height: rem(12px);
    background-color: $medium-gray;
  }
}
