.product-spec {
  @extend %clearfix;
  @include helvetica-arial-light;
  padding: rem(25px) 0;

  .checkbox-wrap {
    padding: 0 $grid-gutter-width/2 rem(15px);
    float: left;

    label {
      @include helvetica-arial-light;
      text-transform: none;
      letter-spacing: rem(1.5px);
    }

    &.compare {
      padding: 0;
      margin-top: rem(-11px);
    }
  }

  &__label {
    div {
      @include helvetica-arial-bold;
      letter-spacing: rem(1px);
    }
  }

  &__label,
  &__value {
    div {
      @include flex-grow(1);
      @include flex-basis(0);
      min-width:rem(90px);
    }
  }

  &__left {
    font-size: rem(18px);
  }

  &__name {
    margin-bottom: rem(20px);
    clear: both;
  }

  &__desc {
    margin-bottom: rem(15px);
  }

  &__price {
    margin-bottom: rem(15px);
    text-align: right;
    div:first-child {
      font-size: rem(16px);
      @include breakpoint($mobile-only) {
        font-size: rem(10x);
      }
    }
  }

  @include breakpoint($mobile-only) {
    &__price {
      text-align:left;
      clear:both;
      margin-bottom: rem(15px);
      div:first-child {
          font-size: rem(10x);
      }
    }
  }

  .node--product_series & {
      padding: rem(50px) 0;
      border: solid 1px $light-gray;
  }

   &__right {
    clear: both;
    max-height: 0;
    overflow: hidden;

    &,
    & p {
      font-size: rem(11px);
    }

    p {
      color: $grey-3;
      margin: 0;
    }

    &.show-spec {
      @include transition( max-height ease-in .2s);
      max-height: rem(1000px);

      @include breakpoint($mobile-only) {
        @include display(flex);
        border-top: rem(1px) solid $gray-c;
        padding-top: rem(20px);

        .product-spec__label,
        .product-spec__value {
          @include flex-grow(1);

          div {
            min-height: rem(35px);
          }
        }
      }
    }
  }

  &__left,
  &__btn {
    text-align: center;
    padding: $grid-gutter-width/2;
  }

  &__options {
    margin-top: rem(15px);
    clear: both;
  }

  @include breakpoint($phone-only) {
    &__btn {
      text-align: left;
    }

    &__img {
      img {
        max-width: rem(170px);
        margin-left: 0;
      }
    }

    &__left {
      padding:0;
    }
  }

  @include breakpoint($mobile-landscape) {
    &__left,
    &__btn {
      display: inline-block;
      vertical-align: middle;
      width: 49%;
    }

  }

  @include breakpoint($tablet) {
    &__btn {
      display: none;
    }

    &__right {
      clear: none;
      max-height: rem(1000px);
    }

    &__left {
      padding-top: 0;
      padding-bottom: 0;
    }

    &__label {
      padding-bottom: rem(10px);
      border-bottom: rem(1px) solid $gray-c;
    }

    &__value {
      padding-top: rem(10px);

      &,
      & p {
        font-size: rem(12px);
      }
    }

    &__label,
    &__value {
      @include display(flex);
      @include align-items(center);

      div {
        padding: 0 rem(10px);
      }
    }

    &__price {
      float: right !important;

      .price {
        font-size: rem(18px);
      }
    }

    .quote-btn-wrap {
      margin-top: $grid-gutter-width/2;
    }
  }

  &__table {
    input[type=checkbox] + label {
      padding-left: 0;
      &:after {
        position: relative;
      }
      
      @include breakpoint($tablet) {
        &:before {
          left: rem(54px);
        }
        &:after {
          margin: 0 rem(54px);
        }
      }
    }

    @include breakpoint($tablet) {
      th:first-of-type {
        text-align: center;
      }
    }

    @include breakpoint($mobile-only){
      .product-spec__name {
        font-size: rem(18px);
        padding-left:0;
      }
    }
  }
}

.product-series-grid {
  &:nth-of-type(even) {
    background-color: $gray-f5;
  }
}

.view--product-series-items {
  .product-spec__label, .product-spec__value {

    & > div:first-of-type, .product-spec__value > div:first-of-type {
      padding-left: 0;
    }

    @include breakpoint($tablet) {
      div {
        min-width: rem(60px);
        word-wrap: normal;
      }
    }

  }

  .product-series-redesign & {
    .cols-4 {
      table-layout: fixed;
    }
  }

  .cols-3 {
    td {
      width: 25%;
    }

    .views-field-field-omar-description {
      width: 50%;
    }
  }
}

.view--product-model-specifications {
  padding-top: rem(5px);
}

.node--product_series {
  .views-row:nth-child(even) .product-spec {
    background: $white;
  }
}
